import React, { useState, Fragment } from 'react';
import { BackDrop,BackdropContainer,Container } from './ModalSection_styled';


const initStyle = {
  width:'30rem',
  //display:'none'
}

const hide = {
  display:'none'
}

const show = {
  display:'flex'
}
function ModalSection(props) {

  const {switchWindow='',handleHideWindow=()=>{}} = props;

  //functions
  const { render=()=>{} } = props

  const [containerStyle,setContainerStyle] = useState(initStyle);
  const [backdropStyle,setBackdropStyle] = useState('')

  
  return (
    <Fragment>
      <BackdropContainer>

          <BackDrop style={switchWindow==='flex' || backdropStyle==='flex'?show:hide} 
            onClick={(event)=>{
              event.stopPropagation()
              handleHideWindow()
              setBackdropStyle('')
            }}
          >
            <Container style={containerStyle} 
              onClick={(event)=>{
                event.stopPropagation()
              }}
            >
              {render({setContainerStyle,setBackdropStyle})}
            </Container>
          </BackDrop>
      </BackdropContainer>
    </Fragment>
  )
}

export default ModalSection
