import styled from 'styled-components';

export const Container = styled.div`
    width: 93.5%;
    margin:auto;
    font-family: Roboto;

`;

export const RouteContainer = styled.div`
    position: relative;
    background-color: white;
`;

export const TitleContainer = styled.div`
    margin:1rem 0;
    padding:1rem 0;
    display: flex;
    align-items: center;

    color:#357593;
`; 

export const Title = styled.div`
    font-size: 3rem;
`;



export const RouteReviewTitleContainer = styled.div`
    margin:1rem 0 2rem;
`;

export const RouteReviewTitle = styled.span`
    letter-spacing: 0.055em;    
    font-weight: bold;

   
`;




export const IdContainer = styled.div`
    position:absolute;
    top: 4.6rem;
    right: 8rem;
`;

export const Id = styled.span`
    font-size:2rem;
    color: #929AAB;
`;

export const BackgroundImageContainer = styled.div``;

export const BackgroundImage = styled.div`
    svg{
        position: absolute;
        top: 1rem;
        right: 0;
        height: 230px;
        width: 230px;
        fill:#FA7D09;
        opacity:6%;
    }
`;

