import React,{useState} from 'react'
import { TableData,TableRow,CustomButton } from './TableInfo_styled'
import ModalSection from 'Components/ModalSection/ModalSection';
import DeleteConfirmation from 'Components/DeleteConfirmation/DeleteConfirmation';
import ModifyUser from 'Pages/Login/Components/ModifyUser/ModifyUser';
import Select from 'react-select';
import { connect } from 'react-redux';
import { updateUser } from 'Redux/Login';


function TableInfo(props) {
  
  const [switchDelete,setSwitchDelete] = useState('');
  const [switchModify,setSwitchModify] = useState('');
  
  const showDeleteWindow = ()=>{
    setSwitchDelete('flex')
  }
  const hideDeleteWindow = ()=>{
    setSwitchDelete('')
  }

  const showCreateWindow = ()=>{
    setSwitchModify('flex')
  }
  const hideCreateWindow = ()=>{
    setSwitchModify('')
  }


  const { data:{email,name,lastName,role},update } = props;
 
  return (
    <TableRow>
      <TableData>
        <input type="checkbox" name="" id=""/>
      </TableData>

      <TableData>{name}</TableData>
      <TableData>{lastName}</TableData>
      <TableData>{email}</TableData>

      <TableData>
      <Select  
          onChange={()=>{

          }}
          option={{value:'',label:''}}>
        </Select>
      </TableData>
        
        <ModalSection
          switchWindow={switchDelete}
          handleHideWindow={hideDeleteWindow}

          render={({setBackdropStyle})=>
            <DeleteConfirmation 
            title={name}
            cancel={()=>{
                setBackdropStyle('');
                setSwitchDelete('');
            }}
            confirm={()=>{
              
            }}/>
          }/>

        <ModalSection
          switchWindow={switchModify}
          handleHideWindow={hideCreateWindow}

          render={({setBackdropStyle})=>
            <ModifyUser 
              userData={props.data}
              title={name}
              requestType='put'
              cancel={()=>{
                setBackdropStyle('')
                setSwitchModify('')
              }}
              save={(info)=>{
                update(info)
                console.log(info)
              }}
            />
          }/>
      <TableData>
        <CustomButton onClick={showCreateWindow}>Editar</CustomButton>
        <CustomButton onClick={showDeleteWindow}>Eliminar</CustomButton>
      </TableData>
    </TableRow>
  )
}

const mapRootStateToProps = state=>{
  return {
    loginReducer:state.LoginReducer,
    user: state.UserReducer
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
    update:(data)=>{dispatch(updateUser(data))}
  }
}

export default connect(null,mapDispatchToProps) (TableInfo)
