import React from 'react'
import { Container,ButtonContainer,ButtonIconContainer,Text,TextContainer } from './StopButton_styled'
import {ReactComponent as Humbugger} from 'Images/humbugger.svg';
import { connect } from 'react-redux';
import { toggleStopsContainer } from 'Redux/ReduxActions';


function StopButton({route,toggleStops}) {
  return (
    <Container>
      <ButtonContainer>
        <TextContainer>
          <Text>{route.position.length} Paradas</Text>
        </TextContainer>
        <ButtonIconContainer onClick={toggleStops}>
          <Humbugger /> 
        </ButtonIconContainer>
      </ButtonContainer>
    </Container>
  )
}

const MapRootStateToProps = (states)=>{
  return {
      route:states.RouteReducer.route
  }
}


const MapDispatchToProps = (dispatch)=>{
  return{
      toggleStops:()=>{dispatch(toggleStopsContainer())}
  }
}
export default connect(MapRootStateToProps,MapDispatchToProps)( StopButton )
