import styled from 'styled-components'
import { motion } from "framer-motion"

export const InputImageContainer = styled(motion.div)`
  position:relative;
`;

export const CustomIcon = styled.div`

  svg{
    position: absolute;
    height: 1.5em;
    width: 1.5em;
    top: 14px;
    left: 40px;
    fill:#666;
  }
`;

export const CustomInput = styled.input`
  font-family: Poppins-Medium;
  font-size: 15px;
  line-height: 1.5;
  color: #666;
  display: block;
  width: 90%;
  background: #e6e6e6;
  height: 50px;
  border-radius: 25px;
  padding: 0 30px 0 68px;


  
  margin: 1rem;
`;

export const WrapInput = styled.div`
  position: relative;
  width: 100%;
  z-index: 1;
  margin-bottom: 10px;
`;
