import React,{useEffect} from 'react'
import { Container,Wrapper } from './User_styled';
import UserGeneralInfo from './Components/UserGeneralInfo/UserGeneralInfo';
import UserLogSection from './Components/UserLogSection/UserLogSection';
import FeedbackSummary from 'Components/Feedback/FeedbackSummary/FeedbackSummary';
import { connect } from 'react-redux';


function User({user}) {
  const { isAdmin } = user;
  useEffect(()=>{
    console.log(isAdmin)

  },[isAdmin])

  return (
    <Wrapper>
      <Container>
        <UserGeneralInfo />
        <div style={{display:'flex',flexDirection:'column',marginRight:'1rem'}}>
          <UserLogSection />
          {isAdmin && <FeedbackSummary /> }
        </div>
      </Container>
    </Wrapper>
  )
}


const mapRootStateToProps = state=>{
  return {
    user:state.UserReducer
  }
}


export default connect(mapRootStateToProps) ( User )
