import React, { useEffect } from 'react'

import LeftContainer from 'Components/LeftContainer/LeftContainer';
import NavBar from 'Layout/Navbar/Navbar';
import 'css/App.css'
import 'css/animations.css'

import { connect } from 'react-redux';
import {  useRouteMatch } from 'react-router-dom';

import { Container,MainContainer } from './Main_styled'
import Footer from 'Layout/Footer/Footer'

import { getUsersWithRoutes } from 'Redux/UserReducer';
import { getRoutes, saveUser } from 'Redux/ReduxActions';

import MainRoute from './MainRoute';
import { getLocalStorageInfo } from 'Lib/global_functions';

function Main(props) {
  let { path } = useRouteMatch();
  const { getUsersAndRoutes,fillRoute,loadUser } = props;


  useEffect(()=>{
    getLocalStorageInfo().user.role === 'ADMIN' && getUsersAndRoutes()

    
    loadUser();
    fillRoute();
  },[]);

  return (
    <div style={{display:'flex',maxWidth:'1980px'}} >
      <LeftContainer/>
      <Container>
        <MainContainer> 
          <NavBar />
          <MainRoute path={path}/>
          <Footer />
        </MainContainer>  
      </Container>
    </div>
  )
}


const mapDispatchToProps = (dispatch)=>{
  return{
    getUsersAndRoutes: ()=>{dispatch(getUsersWithRoutes())},
    fillRoute: ()=>{dispatch(getRoutes())},
    loadUser:(user)=>{dispatch(saveUser(user))}
  }
}

export default connect(null,mapDispatchToProps)(Main)

