import * as types  from './MarkersTypes' 

export const addingMaker = (coords)=>{
  return{
    type: types.ADD_MARKER,
    coords:coords
  }
}
export const updatingMarker = (refData)=>{
  return{
    type:types.UPDATE_MARKER,
    refData:refData
  }
}

export const deleteMarker = (coords)=>{
  
  return {
    type: types.DELETE_MARKER,
    payload:coords
  }
}

export const fillingMarker = (currentRoute)=>{
  return {
    type: types.FILL_MARKER,
    currentRoute:currentRoute
  }
}

export const unloadMarkers = ()=>{
  return {
    type:types.UNLOAD_MARKERS
  }
}


export const animateMarker = (payload)=>{
  
  return {
    type:types.MARKER_CHANGE_ANIMATION,
    payload:payload
  }
}

export const deAnimateMarker = (payload)=>{
  
  return {
    type:types.MARKER_REMOVE_ANIMATION,
    payload:payload
  }
}

export const toggleMarkersVisibility = ()=>{
  
  return{
    type:types.TOGGLE_MARKERS_VISIBILITY
  }
}