import React from 'react'
import RouteDetailContainer from './Components/RouteDetailContainer'

import { Container,RoutesContainer } from './Route_styled'
import { connect } from 'react-redux';
import NoRoutesFound from './NoRoutesFound';

function  Route (props)  {
    const { routes=[] } = props
    if (routes.length > 0 ){
        return (
            <Container>
                <RoutesContainer >
                    {routes.map((route,index)=>{
                        return(
                            <RouteDetailContainer
                                routeInfo={route}
                                key={`route-${index}`}
                            /> 
                        )
                    })}
                </RoutesContainer>
            </Container>
        )
        }else{
            return <NoRoutesFound />
        }
    }
    



export default connect()(Route)

