import React from 'react'
import { InputImageContainer,WrapInput,CustomIcon,CustomInput} from './InputIcon_styled'

const inputContainerVariant = {
  initValue:{opacity:0,x:15},
  endValue:{opacity:1,x:0},
  
}
function InputIcon(props){
  const { data:{placeholder,type="text",name,value,customFunction},children } = props;
  
  return(
    <InputImageContainer 
      variants={inputContainerVariant}
      initial='initValue'
      animate='endValue'>
      <WrapInput></WrapInput>
      <CustomIcon >{children}</CustomIcon>
      <CustomInput 
        placeholder={placeholder} 
        type={type}
        name={name}
        value={value}
        onChange={customFunction}
      ></CustomInput>
    </InputImageContainer>
  )  
}

export default InputIcon