import * as types from './UserTypes'
import { getLocalStorageInfo } from 'Lib/global_functions';
import { defaultLocalStorage } from 'Lib/global_constants';

const initialState = {
    loadingUsers:false,
    isAdmin:false,
    isAuthorized:false,
    user:defaultLocalStorage.resolve,
    users:[]
    
}


const UserReducer = (state = initialState, action)=>{
  
  switch (action.type) {
    case types.IS_ADMIN_TRUE:
      return {
        ...state,
        isAdmin:true
      }
    case types.IS_ADMIN_FALSE:
      return {
        ...state,
        isAdmin:false
      }
    case types.UNLOAD_USER:
      return initialState
    case types.SAVE_USERS:
      const users = action.payload;
      return {
        ...state,
        users:users,
        loadingUsers:false
      }
    case types.SAVE_USER:
      const user = action.payload
      return {
        ...state,
        user:user,
        loadingUsers:false
      }
    case types.LOADING_USERS:
      return {
        ...state,
        loadingUsers:true
      }
    default: return state
  }
}

export {UserReducer}