import React from 'react'
import { LoadingLoginContainer,BounceBallWrapper,
    BounceBallContainer,BounceBall,BounceBallText } from './LoadingAnimation_styled';

function LoadingAnimation() {
  return (
    <LoadingLoginContainer>
      <BounceBallWrapper >
          {['','',''].map((empty,index)=>{
            return( 
              <BounceBallContainer key={`bbContainer-${index}`}>
                <BounceBall key={`bb-${index}`}/>
              </BounceBallContainer>
            )
          })}
        <BounceBallText>Cargando...</BounceBallText>
      </BounceBallWrapper>
    </LoadingLoginContainer>
  )
}

export default LoadingAnimation
