import * as type from './RouteTypes';
import bddHelper from 'BDD/Request';



export const handleInputChange = (event,some)=>{
  if('label' in event){
    event.target = {};
    event.target.name = some.name
    event.target.value = event.value;
  }

  return {
      type: type.ROUTE_CHANGE,
      event:event
  }
}

export const AddingRoutePosition = (routePositionInfo)=>{
  return {
    type:type.ADD_POSITION,
    routePositionInfo:routePositionInfo
  }
}

export const getGeolocationAndUpdatingRoutePosition = (refData)=>{
  const { lat,lng,currentPosition } = refData;
  console.log(refData)
  return(dispatch=>{
  bddHelper.getGeocodeRequest(lat,lng)
    .then((street_name)=>{
      dispatch( updatingRoutePosition({lat,lng,currentPosition,street_name}) )
    })
  })
}
export const updatingRoutePosition = (refData)=>{
  return {
    type: type.UPDATE_POSITION,
    refData:refData
  }
}

export const deleteRoutePosition = (coords)=>{
  return {
    type:type.DELETE_POSITION,
    payload:coords
  }
}

export const onChangeRouteTransportType = (transportType)=>{
  return{
    type:type.CHANGE_ROUTE_TRANSPORT_TYPE,
    transportType:transportType
  }
}

export const fillRoute = (route)=>{
  return{
    type:type.FILL_ROUTE,
    route:route
  }
}

export const unloadRoute = ()=>{
  return {
    type:type.UNLOAD_ROUTE
  }
}

export const routeLoading = ()=>{
  return {
    type: type.ROUTE_LOADING
  }
}

export const routeError = ()=>{
  return {
    type: type.ROUTE_ERROR
  }
}

export const routeSuccess = ()=>{
  return {
    type: type.ROUTE_SUCCESS
  }
}

export const routeResetSavingStatus = ()=>{
  return {
    type: type.ROUTE_SAVINGSTATUS_RESET
  }
}