import React from 'react'
import { Container, LogSection,Title } from './UserLogSection_styled'

import Log from './Components/Log';

function UserLogSection() {
  return (
    <Container>
      <Title>Historial Inicio de Sesion</Title>

      <LogSection>
        <Log />
        <Log />
        <Log />
        <Log />
      </LogSection>

    </Container>
  )
}

export default UserLogSection
