import React from 'react'
import { Container,ProfilePictureContainer,ProfilePicture,SectionWrapper,
  PersonalInfoContainer,PersonalInfo,Role,RoleState,Circle,AssociationInformationContainer,AssociationInformation,
  RandomInformationContainer,RandomInformation,RandomInformationNumber,SectionBottomWrapper } from './UserGeneralInfo_styled'

import UserEditSection from '../UserEditSection/UserEditSection'
import { ReactComponent as Facebook } from 'Images/facebook.svg'
import { ReactComponent as Twitter } from 'Images/twitter.svg'
import { ReactComponent as Instagram } from 'Images/instagram-color.svg'
import EditSocialLinks from 'Components/EditSocialLinks/EditSocialLinks'
import { connect } from 'react-redux'

function UserGeneralInfo({user}) {
  const {user:userInfo,isAdmin} = user;

  const firstLetterToUpperCase = (string)=>{
    const firstLetter = string.charAt(0).toUpperCase();
    const restOfLetter = string.substring(1,string.length)

    const newString = firstLetter+restOfLetter
    return newString
  }

  const name = firstLetterToUpperCase(userInfo.name);
  const lastName = firstLetterToUpperCase(userInfo.lastName);

  return (
    <div>
      <Container>
        <div style={{display:'flex',flexDirection:'column'}}>
          <SectionWrapper>
            <ProfilePictureContainer>
              <ProfilePicture>  <p style={{marginRight:'0.2rem'}}>{name.substring(0,1)}</p> <p>{lastName.substring(0,1)}</p></ProfilePicture>
            </ProfilePictureContainer>

            <PersonalInfoContainer>
              <PersonalInfo> <p>{name} {lastName}  </p> <RoleState> {userInfo.role}</RoleState> </PersonalInfo>
              <p>Algun texto para rellenar</p>
              <Role><Circle /> {userInfo.active ? 'Activo' : 'Inactivo'}</Role>
            </PersonalInfoContainer>

            <AssociationInformationContainer>
              <AssociationInformation>FENATRADO</AssociationInformation>
              <p>Alguna Informacion de fenatrado</p>
            </AssociationInformationContainer>
          </SectionWrapper>

          

          <UserEditSection/>

          <SectionBottomWrapper>
            <RandomInformationContainer>
              <RandomInformation><span>Rutas Totales</span></RandomInformation>
              <RandomInformationNumber>45</RandomInformationNumber>
            </RandomInformationContainer>

            <RandomInformationContainer>
              <RandomInformation><span>Personal Por ruta</span></RandomInformation>
              <RandomInformationNumber>145</RandomInformationNumber>
            </RandomInformationContainer>

            <RandomInformationContainer>
              <RandomInformation> <span>ALgo que poner aqui</span> </RandomInformation>
              <RandomInformationNumber>15</RandomInformationNumber>
            </RandomInformationContainer>

            <RandomInformationContainer>
              <RandomInformation> <span>Klk guare</span> </RandomInformation>
              <RandomInformationNumber>10</RandomInformationNumber>
            </RandomInformationContainer>
          </SectionBottomWrapper>
        </div>
        
      </Container>

      {isAdmin &&
      <Container>
            <div style={{margin:'3rem 1rem 0',display:'flex',justifyContent:'space-between',width:'100%'}}>
              <EditSocialLinks >
                <Facebook />
              </EditSocialLinks>

              <EditSocialLinks >
                <Instagram />
              </EditSocialLinks>

              <EditSocialLinks >
                <Twitter />
              </EditSocialLinks>
            </div> 
        </Container>
      }
    </div>
  )
}
const mapRootStateToProps = state=>{
  return {
    user:state.UserReducer
  }
}


export default connect(mapRootStateToProps) ( UserGeneralInfo )
