import styled from 'styled-components'


export const RouteReviewIcon = styled.div`
    position:relative;
    display:flex;
    overflow:hidden;
    letter-spacing: 0.085em;
    width: 6rem;
    height:1.3rem;
    font-weight: bold;
    

    svg{
        position:absolute;
        top: -1.6rem;
        left:-.3rem;
        fill:white;
        height: 100px;
        width: 100px;

        path{
            fill:white;
        }
    }
`;

export const StarBackground = styled.div`
        background-color:#357593;
        fill:white;
        height: .7rem;
        width: 2rem;
`;
export const StarBackgroundContainer = styled.div`
        background-color:gray;
        position:absolute;
        top: .4rem;
        left: -.3rem;
        fill:white;
        height: .7rem;
        width: 5.5rem;
`;