import React,{useState} from 'react'
import NavbarIcon from './Components/NavbarIcon/NavbarIcon'
import NavbarButton from './Components/NavbarButton/NavbarButton'
import NavbarDropDown from './Components/NavbarDropDown/NavbarDropDown';

import { Container,CentralBar,BarContainer,Section,NavbarWrapper,LogoContainer,Logo } from './NavBar_styled'
import { Link} from "react-router-dom";
import { connect } from 'react-redux';
import NameInitials from './Components/NameInitials/NameInitials';


function NavBar(props) {
  const [hide, setHide] = useState(true)
  const { user:{user,isAdmin} } = props;

  const handleDropDown = ()=>{
    setHide(!hide)
  }

  const { name,lastName } =  user;
  return (
    <NavbarWrapper>
      <Container>
        <CentralBar>
          <BarContainer>
            <Section >
              <Link to="/main">
                <LogoContainer>
                  <Logo>Doble</Logo>
                  <Logo style={{color:'#f5b022'}}>Via</Logo>
                </LogoContainer>
              </Link>
            </Section>

            <Section >
              <NavbarButton  buttonText="Inicio"/>
              <NavbarButton goTo="/main/user" buttonText="Configurar"/>
              <NavbarIcon >
                <NameInitials onClickHandler={handleDropDown} name ={name}lastName={lastName}/>
                <NavbarDropDown  hidden={hide}/>
              </NavbarIcon>
            </Section>

          </BarContainer>
        </CentralBar>
      </Container>
    </NavbarWrapper>
  )
}
const mapRootStateToProps = state=>{
  return {
    user:state.UserReducer
  }
}
export default connect(mapRootStateToProps)( NavBar )

