import React from 'react'


import { ReactComponent as LocationPin } from 'Images/room-24px.svg'
import { ReactComponent as Watch } from 'Images/watch_later-24px.svg'
import { ReactComponent as ReviewIconSvg } from 'Images/rate_review-24px.svg' 

import { LeftSideSectionContainer,Destination,DestinationContainer,
    DestinationIcon,Time,TimeContainer,Review,ReviewContainer,ReviewIcon } from './LeftSideSection_styled'
const svgStyle = {
  height:'1.5rem',
  width:'1.5rem'
}
function LeftSideSection(props){

    const { destination,review=[],schedule:{interval,endTime,startTime} } = props.routeInfo;

    return(
        <LeftSideSectionContainer>
            <DestinationContainer>
            <DestinationIcon> 
                <LocationPin style={svgStyle}/> 
            </DestinationIcon>
            <Destination>{destination}</Destination>
        </DestinationContainer>
            <DestinationContainer>
                <DestinationIcon> 
                    <Watch style={svgStyle}/> 
                </DestinationIcon>
                <Destination>{interval}</Destination>
            </DestinationContainer>

            <TimeContainer>
                <Time>{startTime} - {endTime}</Time>
            <ReviewContainer>
                <Review>{review.length}</Review>
                <ReviewIcon><ReviewIconSvg style={svgStyle}/></ReviewIcon>
            </ReviewContainer>
            </TimeContainer>

        </LeftSideSectionContainer>
    )
}


export default LeftSideSection
