
import styled from 'styled-components'
import motion from 'framer-motion'

export const BackgroundImage = styled.div`

  img{
    position:absolute;
    right: 2rem;
  }
`;

export const CustomButton = styled.div`
  background-color:#4587a5;
  padding:.2rem 1rem;
  cursor:pointer;

  position:absolute;
  right: 5.4rem;
  bottom:0;

  color:white;
  border-radius:5px;

  transition:300ms;
  :hover{
    background-color:#519cbc;
  }
`;

export const Container = styled.div`
  position:relative;
  margin:1rem;
  padding:1rem;
  border-radius:20px;
  box-shadow:hsla(0, 0%, 15%, 0.1) 0px 1px 15px 1px;
`;

export const InputSectionContainer = styled.form`
  display:flex;
`;

export const InputSection = styled.div`
  position:relative;
  display:flex;
  justify-content:space-between;
  flex-wrap:wrap;
  margin-bottom:1.2rem;
`;

export const InputContainer = styled.div`
  

`
export const InputLabel = styled.label`
  position:relative;
  left:.4rem;
  font-size:.9rem;
  font-weight:500;
  margin-bottom:.2rem;


  color: gray;
  display:block;
`;
export const Input = styled.input`
  text-align:center;

  border:1px solid gray;
  border-radius:10px;
`

