import { addMarkerPolylineRoutePosition } from '../CombinedActions'
import * as types  from './MapTypes'

export const OnMapClick = ({lat,lng})=>{
  return((dispatch)=>{
    dispatch(addMarkerPolylineRoutePosition({lat,lng}))
  })
}


export const testing = (googleProps)=>{
  console.log(googleProps);
  return{
    type:''
  }
}

export const saveMapInfo = (mapInfo)=>{
  return{
    type:types.SAVE_MAP_INFO,
    payload:mapInfo
  }
}

export const panToHandler = (mapRef,{lat,lng,zoom=14})=>{
  mapRef.current.panTo({ lat, lng });
  mapRef.current.setZoom(zoom);
}

export const switchClicked = (bool)=>{
  return{
    type:types.SWITCH_CLICKED,
    payload:bool
  }
}

export const saveCurrentPosition = ({lat,lng})=>{
  return{
    type:types.SAVE_CURRENT_POSITION,
    payload:{lat,lng}
  }
}


export const unloadMap = ()=>{
  return{
    type:types.UNLOAD_MAP
  }
}