import React, { useState } from 'react'


import { ReactComponent as Construction } from 'Images/construction.svg'
import { ReactComponent as Clear } from 'Images/clear.svg'
import { connect } from 'react-redux'
import { toggleConfirmationMessage, deleteRoute } from 'Redux/ReduxActions'
import { loadApp } from 'Redux/CombinedActions'
import { RightSideSectionContainer,PriceContainer,Price,ControlIconsContainer,ControlIcon} from './RightSideSection_styled'
import ModalSection from 'Components/ModalSection/ModalSection'
import DeleteConfirmation from 'Components/DeleteConfirmation/DeleteConfirmation'

export const construction = {
  fill:'#196386',
  height:'2rem',
  width:'2rem'
}
export const clear = {
  fill:'#E70000',
  height:'2.2rem',
  width:'2.5rem'
}



function RightSideSection(props){
  
  const { price,ID } = props.routeInfo;
  const { fillingApp,deleteRoute } = props;

  const [switchDelete,setSwitchDelete] = useState('');

  
  const handleClear = (evt)=>{
    evt.stopPropagation();
    //switchConfirmationMessage(confirmationMessage,ID)
    showDeleteWindow()
  }
  
  const handleConstruction = (evt)=>{
    evt.stopPropagation();
    fillingApp(props.routeInfo)
    console.log('working 2')
  }
  
  const showDeleteWindow = ()=>{
    setSwitchDelete('flex')
  }
  const hideDeleteWindow = ()=>{
    setSwitchDelete('')
  }

  return(
    <RightSideSectionContainer>
      <ModalSection
        switchWindow={switchDelete}
        handleHideWindow={hideDeleteWindow}

        render={({setBackdropStyle})=>
          <DeleteConfirmation 
            title='Ruta'
            message='Seguro que desea Elimnar?'
            cancel={()=>{
              setBackdropStyle('')
              setSwitchDelete('')
            }}

            confirm={()=>{
              deleteRoute(ID)
              setBackdropStyle('')
              setSwitchDelete('')
            }}/>
        }/>
        <PriceContainer>
            <Price>${price}</Price>
        </PriceContainer>
        <ControlIconsContainer>
            <ControlIcon onClick={handleConstruction}><Construction style={construction}/></ControlIcon>
            <ControlIcon onClick={handleClear}><Clear style={clear}/></ControlIcon>     
        </ControlIconsContainer>
    </RightSideSectionContainer>  
  )
}

const mapRootStateToProps = state=>{
  return {
      states:state
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
      fillingApp: (route)=>{dispatch(loadApp(route))},
      switchConfirmationMessage: (payload)=>{dispatch(toggleConfirmationMessage(payload))},
      deleteRoute: routeId=>{dispatch(deleteRoute(routeId))}
  }
}


export default connect(mapRootStateToProps,mapDispatchToProps)(RightSideSection)
