import { FILL_ROUTES, UNLOAD_ROUTES } from './RoutesTypes'

const initState = {  
  routes:[]
}

const RoutesReducer = (state = initState, action) =>{
  switch (action.type) {
    case FILL_ROUTES:
      const routes = action.routes;
      return{
        routes:routes
      }
    case UNLOAD_ROUTES:
      return initState
    default:
      return state;
  }
  
}



export {RoutesReducer};

