import React,{Fragment} from 'react'
import styled from 'styled-components'

const CCustomButton = styled.button`
    padding:.6rem;
    margin:1rem;
    color:white;
    background-color:#2b6cb0;
    border-radius:30px;

  `;

function CustomButton({text,_function=()=>{},type}) {
  //console.log(_function())
  return (
    <Fragment>
      <CCustomButton 
        type={type}
        onClick={(event)=>{
          event.stopPropagation();
          _function()
        }}>
        {text}
      </CCustomButton>
    </Fragment>
  )
}


export default CustomButton
