import React,{Fragment} from 'react'

import { Cards,HoldPicture,CustomImage } from './CentralCard_styled'
import { transportCardInformation,statisticCardInformation,cardInformation,data } from './constants'

//import transportation from '../Images/transportation.jpg'
import reviewPicture from 'Images/reviews.svg'
import statistic from 'Images/statistic.jpg'



import { connect } from 'react-redux';

import { HorizontalBar,Line } from 'react-chartjs-2'


import CardContainer from 'Components/CardContainer/CardContainer';
import Reviews from 'Components/Reviews/Reviews';
import MapContainer from 'Components/Map/MapContainer/MapContainer';
import RouteCard from 'Components/RouteCard/RouteCard';
import Table from 'Components/Table/Table'



function CentralCards (props){
  const route   = props.route;
  const {review:reviews=[]} = route;

  return (
    <Fragment>
      <MapContainer />

      <Cards>
        <CardContainer 
          style={{marginRight:'.5rem'}} 
          cardInfo={transportCardInformation}>
          <RouteCard />
        </CardContainer>


        <CardContainer  
          style={{marginRight:'.5rem'}}  
          cardInfo={statisticCardInformation}>
          <HoldPicture>
            <CustomImage srcSet={statistic} alt="" />
          </HoldPicture>
        </CardContainer>
        
        <CardContainer  cardInfo={cardInformation}>
          { reviews.length > 0 ? reviews.map((data,index)=>{
            return(
              <Reviews key={index} data={data}/>
            ) 
          }) 
          :<HoldPicture>
            <CustomImage srcSet={reviewPicture} alt="" />
          </HoldPicture>
          } 
        </CardContainer> 

        
        <CardContainer  
          style={{marginRight:'4rem'}} 
          cardInfo={statisticCardInformation}>
          <HorizontalBar data={data}/>
        </CardContainer>

        <CardContainer  cardInfo={statisticCardInformation}>
          <Line data={data} options={{ maintainAspectRatio: false }} width={100} height={340}/>
        </CardContainer>

      </Cards>

      <Table /> 
    </Fragment>
    
  )
}


const mapRootStateToProps = state=>{
  return {
    route:state.RouteReducer.route
  }
}





export default connect (mapRootStateToProps)(CentralCards)


