import styled from 'styled-components';


export const CancelButtonContainer = styled.div`
    display:flex;
    flex-grow:2;

    align-items:flex-end;
    justify-content:flex-end;

    position:relative;
    
`;

export const CancelButton = styled.button`
    position:absolute;
    right: 0;
    
    
    padding:.35rem 1.5rem;
    color:white;
    border-radius:5px;
    background-color:#357593;
    transition:300ms;
    @media (max-width:768px) {
        margin: 2rem 2rem;
    }

    :hover{
        background-color:#4587a5;
    }


`;

export const FormInputContainer = styled.form`
    animation:fadeIn 1s;
    max-height:95vh;
    padding: 0.5rem 3rem;;
    overflow:scroll;
    

    --scrollbarBG: #CFD8DC;
    --thumbBG: #90A4AE;
    
    ::-webkit-scrollbar {
        width: 10px;
        display: none;
    }
    
    scrollbar-width: thin;
    scrollbar-color: var(--thumbBG) var(--scrollbarBG);
    
    ::-webkit-scrollbar-track {
        background: var(--scrollbarBG);
    }
    ::-webkit-scrollbar-thumb {
        background-color: var(--thumbBG) ;
        border-radius: 6px;
        border: 3px solid var(--scrollbarBG);
    }
`;


export const CustomSubmitButton = styled.button`
    background-color:#357593;
    transition:300ms;
    @media (max-width:768px) {
        margin: 2rem 2rem;
    }

    :hover{
        background-color:#4587a5;
    }
    
`;

export const Testing = styled.div`
  position:flex;
  justify-content:center;
  align-items:center;

  display: inline-block;
  height:5rem;
  width: 100%;

  position:relative;
`;




export const LineDivider = styled.div`

  margin: 1rem auto;
  display: block;
  width:16rem;
  border-color:gray;
  border-bottom:solid 2px;
  opacity:0.1;
  box-shadow: 5px 5px 10px #c2c0bc;
`;