import React,{useState} from 'react'
import CustomizeInput from 'Components/CustomInput/CustomizeInput'
import CustomButton from 'Components/CustomButton/CustomButton'
import EditableDropDown from 'Components/EditableDropDown/EditableDropDown';

import { connect } from 'react-redux'
import { handleUserChange, getAllUsers, updateUser, createUser } from 'Redux/ReduxActions';
import { Container,Title,InputContainer,ButtonContainer } from './ModifyUser_styled';
import { creatingInputData, selectInputData } from './constants';


function ModifyUser({title='Missing Info',save,cancel,requestType='post',userData,userReducer,updateUser}) {
  
  const [user,setUser] = useState({
    name:userData.name,
    lastName:userData.lastName,
    email:userData.email,
    role:userData.role
  });

  const { token } = userReducer;
  const users = creatingInputData(user)
  
  const HandleChange = (evt,some)=>{
    
    if('label' in evt){
      evt.target = {};
      evt.target.name = some.name
      evt.target.value = evt.value;
    }
    const name = evt.target.name;
    const value = evt.target.value;
    
    setUser({
      ...user,
      [name]:value
    })
  }

  const handleSubmit =  (evt)=>{
    evt.preventDefault();
    updateUser({...user,userId:userData.ID,token});
  }

  return (
    <Container>
      <form onSubmit={handleSubmit}>
        <Title>{title}</Title>

        <InputContainer>
          <CustomizeInput _function={HandleChange} inputData={users.name} />
          <CustomizeInput _function={HandleChange} inputData={users.lastName}/>
          <CustomizeInput _function={HandleChange} inputData={users.email}/>
          <EditableDropDown 
            _function={HandleChange}
            editable={false}
            containerStyle={{width:'82%',right:'.4rem'}}
            options={selectInputData}
            inputData ={users.role}/>
        </InputContainer>
        
        <ButtonContainer>
          <CustomButton text={requestType==='post'?"Crear":"Guardar"} _function={save.bind('',user)} type='submit'/>
          <CustomButton text="Cancelar" _function={cancel} type='button'/>
        </ButtonContainer>

      </form>
    </Container>
  )
}

const mapRootStateToProps = state=>{
  return {
    loginReducer:state.LoginReducer,
    userReducer:state.UserReducer
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
    handleChange:event=>{dispatch(handleUserChange(event))},
    getAllUsers:()=>{dispatch(getAllUsers)},
    updateUser:(data)=>{dispatch(updateUser(data))},
    createUser:()=>{dispatch(createUser)}
  }
}
export default connect (mapRootStateToProps,mapDispatchToProps)(ModifyUser)

