import axios from 'axios'
import { getLocalStorageInfo } from 'Lib/global_functions';
const baseURL = 'https://maps.googleapis.com/maps/api/geocode/json?';

const token = getLocalStorageInfo().token;

const matchingRoutes = (routesReceived=[],user)=>{
    const { ID:userId} = user;
    return {
      user:user,
      routes:routesReceived.filter((route)=>{
        return route.ownerID === userId 
      })
    }
  }

const bddHelper = {

getAllUsers (){
    return axios(
        {
            method:'get',
            url:'https://safe-inlet-31030.herokuapp.com/api/user/all-users',
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY,
                'authorization':`Bearer ${token}`
            },
        })
        .then(res=>{
            return res.data.resolve
        })
        .catch(err=>{
            console.log(err)
        })  
},
getRequest (url) {
    return axios(
        {
            method:'get',
            url:url,
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY,
                'authorization':`Bearer ${token}`
            }
        }
    )
    .then(res=>{
        const result = res.data.resolve;
        return result
    })
    .catch(err=>{
        console.log(err)
    })
},

getGeocodeRequest(lat,lng){
    const url = `${baseURL}latlng=${lat},${lng}&key=${process.env.REACT_APP_GOOGLE_GEOCODE}`;
    return axios.get(url)
    .then(res=>{
        const result = res.data.results[0].formatted_address
        return result
    }).catch(err => {
        console.log(err);
    });

},

postRequest(url, data_received){
    const {ID,routeName,schedule,price,destination,aditionalInfo,position,
        ownerID} = data_received;
    return axios(
        {
            method:'post',
            url:url,
            data:{
                ID:ID,
                routeName:routeName,
                schedule:schedule,
                price:price,
                destination:destination,
                aditionalInfo:aditionalInfo,
                position:position
            },
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY,
                'authorization':`Bearer ${token}`
            }
        }
    )
},

putRequest(url, data_received,redoTrayectory){
    console.log(redoTrayectory)
    const {ID,routeName,schedule,price,destination,aditionalInfo,position,
        ownerID} = data_received;
    return axios(
        {
            method:'put',
            url:`${url}/${ID}`,
            data:{
                redoTrayectory:redoTrayectory,
                ownerID:ownerID,
                routeName:routeName,
                schedule:schedule,
                price:price,
                destination:destination,
                aditionalInfo:aditionalInfo,
                position:position
            },
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY,
                'authorization':`Bearer ${token}`
            }
        }
    )
    .then(res=>{
        const result = res;
        console.log(res);
        return result
    }).catch(err=>{
        console.log(err)
    });
},

deleteRequest(url, routeID){
    return axios  (
        {
            method:'delete',
            url:`${url}/${routeID}`,
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY,
                'authorization':`Bearer ${token}`
            }
        }
    )
    .then(res=>{
        const result = res;
        return result
    }).catch(err=>{
        console.log(err)
    });
},
CreateUser(email,password,role){
    return axios({
        method:'post',
        url:`https://safe-inlet-31030.herokuapp.com/api/user/create-user`,
        headers:{
            'key':process.env.REACT_APP_DOBLE_VIA_KEY,
            'email':email,
            'password':password,
            'role':role,
            'authorization':`Bearer ${token}`
        }
    })
},
UpdateUser({name,lastName,email,password,role,userId,active=true}){
    return axios({
        method:'put',
        url:`https://safe-inlet-31030.herokuapp.com/api/user/update-user`,
        headers:{
            'key':process.env.REACT_APP_DOBLE_VIA_KEY,      
            'authorization':`Bearer ${token}`
        },
        data:{
            'ID':userId,
            'role':role,
            'active':active
        }
    })
},
DeleteUser(userId){
    return axios({
        method:'delete',
        url:`https://safe-inlet-31030.herokuapp.com/api/user/delete-user/${userId}`,
        headers:{
            'key':process.env.REACT_APP_DOBLE_VIA_KEY,
            'authorization':`Bearer ${token}`
        }
    })
}
,

Login(email,password){
    return axios({
        method:'post',
        url:`https://safe-inlet-31030.herokuapp.com/api/user/signin`,
        headers:{
            'key':process.env.REACT_APP_DOBLE_VIA_KEY,
        },
        data:{
            email:email,
            password:password
        }
    })
},

Register(name,lastName,email,password){
    return axios({
        method:'post',
        url:`https://safe-inlet-31030.herokuapp.com/api/user/signup`,
        data:{
            'name':name,
            'lastName':lastName,
            'email':email,
            'password':password,
        },
        headers:{
            'key':process.env.REACT_APP_DOBLE_VIA_KEY,
            'authorization':`Bearer ${token}`
        }
    })
    },UsersWIthRoutes(){
        return bddHelper.getAllUsers(token)
        .then((userAPI)=>{
            return bddHelper.getRequest('https://safe-inlet-31030.herokuapp.com/api/newroute/allRoutes',token)
            .then((routes)=>{
                const result = userAPI.map((data)=>{
                    return matchingRoutes(routes,data)
                }) 
        
                return result
            })
            .catch((err)=>{
                console.log(err)
            })
        })
        }
}



export default bddHelper