export const creatingInputData = (userState)=>{
  return {
    name:{placeholder:'Nombre',type:'text',name:'name',value:userState.name},
    lastName:{placeholder:'Apellido',type:'text',name:'lastName',value:userState.lastName},
    email:{placeholder:'Correo Electronico',type:'text',name:'email',value:userState.email},
    oldPassword:{placeholder:'Contraseña pasada',type:'text',name:'oldPassword',value:userState.oldPassword},
    newPassword:{placeholder:'Contraseña nueva',type:'text',name:'newPassword',value:userState.newPassword},
    password:{placeholder:'Contraseña',type:'text',name:'password',value:userState.password},
    role:{placeholder:'Rol',type:'text',name:'role',value:userState.role}
  }
}

