import React,{useState, Fragment} from 'react'
import {Container,ChildContainer,
  FormContainer,Header,LoginButton,BottomContainer,Span,SignUp,InputContainer,ErrorMessage} from './Login_styled'

import { useFormik } from 'formik';
import { connect } from 'react-redux';
import { loggingUser,registerUser } from 'Redux/Login'
import InputIcon from './Components/InputIcon/InputIcon'
import { useHistory  } from 'react-router-dom'

import { createInputArray,containerVariants} from './constants'
import LoadingAnimation from './Components/LoadingAnimation/LoadingAnimation';
import { getLocalStorageInfo } from 'Lib/global_functions';
import { resetApp } from 'Redux/CombinedActions';


function Login({logUser,SignUpUser,states,unloadApp}) {
  
  const history = useHistory();
  const [register,setRegister] = useState(false);
  const [authorizedFailed,setAuthorizedFailed] = useState(false);

  const { user } = states.LoginReducer;

  const isLoading = states.LoginReducer.user.loadingLogIn;
  const error = states.LoginReducer.user.logInError;
  
  const validUser = getLocalStorageInfo().isAdminOrProvider();

  
  if(user.loggedIn ){
    if(validUser){
      history.push("/main")
    }else{
      unloadApp()
    }
  }

  const formik = useFormik({
    initialValues:{
      name:'',
      lastName:'',
      email:'',
      password:''
    },
    onSubmit: values=>{
      const {name,lastName,email,password} = values;
      setAuthorizedFailed(false)
        register
        ? SignUpUser({name,lastName,email,password})
        : logUser({email,password}) 
    }
  }) 

  //Making input array
  const RegisterInfo = createInputArray(formik,true)
  const LogInfo = createInputArray(formik,false)

  return (
    <Container 
      variants={containerVariants}
      animate='animateBackground'>
        <ChildContainer className={error?'child':''}>
        
          <FormContainer >
            <Header > {register ? "Registra":"Accede a"}  tu cuenta</Header>
            
            {register 
              ? <ErrorMessage 
                  initial={{opacity:0,y:15}} 
                  animate={{opacity:1,y:15,transition:{delay:1,duration:5}}}>
                {error && 'Todos los campos deben estar completos'}
              </ErrorMessage>

              : <ErrorMessage 
                  initial={{opacity:0,y:15}} 
                  animate={{opacity:1,y:15,transition:{delay:1,duration:5}}}>
                {error && 'Contrasena o Email incorrecto'}
              </ErrorMessage>
            }

            {authorizedFailed && <ErrorMessage
              initial={{opacity:0,y:15}} 
              animate={{opacity:1,y:15,transition:{delay:1,duration:5}}}
            >
              Este Usuario no tiene acceso
              </ErrorMessage>}
            
            <form onSubmit={formik.handleSubmit }>
              {register 
              ? RegisterInfo.map((data,index)=>{
                  return <InputContainer key={`input1-${index}`}>
                    <InputIcon  data={data}> {data.icon} </InputIcon> 
                  </InputContainer>  
                })
              : <Fragment> 
                  {LogInfo.map((data,index)=>{
                    return(
                      <InputIcon key={`input-${index}`} data={data}>
                        {data.icon}
                      </InputIcon>
                    )})
                  }
                </Fragment>
              }
              <LoginButton 
                whileHover={ {scale: 1.1} } 
                type="submit" >
                {register ? "Registrar":"Acceder"}
              </LoginButton>
              
            </form>
          </FormContainer>
          
          {isLoading && 
            <div  
              style={{position:'absolute',bottom:'10rem',left:'7em'}}>
              <LoadingAnimation />
            </div>
          }
          
          <BottomContainer>
            <Span>y si no Registrate</Span>
            <SignUp  href="#" onClick={()=>{
              setRegister(!register)
              }}>
              {register ?"Accede":"Registrate"}
            </SignUp>
          </BottomContainer>
        </ChildContainer>

    </Container>
  )
}


const mapRootStateToProps = state=>{
  return {
    states:state
  }
}
const mapDispatchToProps = (dispatch)=>{
  return{
    logUser:(payload)=>{dispatch(loggingUser(payload))},
    SignUpUser: (payload)=>{dispatch(registerUser(payload))},
    unloadApp:()=>{dispatch(resetApp())}
  }
}

export default connect(mapRootStateToProps,mapDispatchToProps) (Login)

