import * as types from './MapTypes'
import mapStyles from './Files/mapStyles'

const initState = {
  zoom: 14,
  mapRef:{},
  google:{},
  currentPosition:{},
  clicked:false,
  mapLoaded:false,
  initial_center:{
    lat: 18.476229,
    lng: -69.868778
  },
  center:{
      lat: 40.854885,
      lng: -88.081807
  },
  styles:mapStyles
}


const MapReducer = (state = initState, action) =>{
  switch (action.type) {
    case types.PAN_TO:
      return state
    case types.UNLOAD_MAP:
      return initState
    case types.SWITCH_CLICKED:
      const value = action.payload
      return {
        ...state,
        clicked:value
      }
    case types.SAVE_CURRENT_POSITION:
      const {lat,lng} = action.payload
      return{
        ...state,
        currentPosition:{lat,lng}
      }
    case types.MAP_CLICK:
      return state 
    case types.SAVE_MAP_INFO:
      const { mapRef,mapObject } = action.payload;
      return {
        ...state,
        mapRef:mapRef,
        google:mapObject,
        mapLoaded:true
      }
    default:
      return state;
  }
  
}

export  {MapReducer};