
import styled from 'styled-components'
import motion from 'framer-motion'

export const Container = styled.div`

  border-radius: 15px;

  box-shadow: hsla(0,0%,15%,0.1) 0px 1px 15px 1px;
`;


export const LinkWrapper = styled.div`
  background-color:rgba(0, 0, 0, 0.05);

  
  border-radius: 15px;

  display: flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;

  padding-top:2rem;

  position:relative;
  width: 13rem;

  margin:0 0rem;

`;
export const Icon = styled.div`
  border: 2px solid rgba(0, 0, 0, 0.05);
  box-sizing: border-box;

  border-radius:100px;
  width: 5rem;
  height: 5rem;

  display:flex;
  justify-content:center;
  align-items:center;
  background-color:white;


  position: absolute;
  top: -2.8rem;

  svg{
    height: 2rem;
    width: 1.5rem;
  }
`;
export const Title = styled.span`
  color:#3B7885;

  font-family: 'Roboto';
  font-style: normal;
  font-weight: 600;
  font-size: 1.6rem;
  text-align: center;

  letter-spacing: 0.13em;

  margin-bottom:.2rem;

`;

export const Input = styled.input`
  border: 2px solid #357593;
  border-radius: 12px;
  width:70%;
  padding:.1rem;
  text-align:center;

  
`;

export const Button = styled.div`
  border-radius: 21px;
  padding:.1em 1rem;

  background-color: #3B7885;
  color:white;

  margin:.3rem 0 1rem;
  span{
    font-weight: bold;
  }
`;

export const ButtonText = styled.div``

