
import styled from 'styled-components'
import motion from 'framer-motion'

export const Container = styled.div`
  display:flex;
  position:relative;
  flex-direction:column;
  

  margin:1rem 6rem 0rem;
`;

export const BackgroundImage = styled.div`
 
  svg{
    width:100%;
    height:100%;
  }
`;

export const Image = styled.div`
  margin-bottom:1rem;
  width: 20rem;
  svg{
    width: 100%;
    height: 100%;
    
  }
`;


export const InfoContainer = styled.div`
  display: flex;
  align-items:center;
  margin: .5rem 0;

  width: 20rem;
`;

export const InfoSvg = styled.span`
  
  svg{
    margin-right: 1.2rem;
    display: inline-block;
    opacity:0.6;
  }
`;

export const InfoMessage = styled.span`
  display: inline-block;
  
  font-weight: bold;
  font-size: .8rem;
  letter-spacing: 0.13em;

  position:relative;
  top:.2rem;

  color: #196386;
`;
