import React from 'react'

import { connect } from 'react-redux';

import { RouteInfoContainer, RouteInfo, Icon,IconTextContainer,
        Text } from './RouteInfoSection_styled';

import {ReactComponent as Stop} from 'Images/stop.svg';
import {ReactComponent as TrafficLight} from 'Images/traffic-24px.svg';
import {ReactComponent as Watch} from 'Images/watch_later-24px.svg';

const isGreaterThanCero = (text)=>{
    
    if (text.length === 0 ){
        return false
    }else{
        return true
    }  
}


function  RouteInfoSection ({position,interval,statusCode,statusReason})  {

    const returningInformationState = (state,customText)=>{
        return isGreaterThanCero(state) ? state : customText
    }
    return (
        <RouteInfoContainer>
            <RouteInfo>
                <IconTextContainer>
                    <Icon><Stop /></Icon>
                    <Text>{position.length}</Text>
                </IconTextContainer>

                <IconTextContainer>
                    <Icon><Watch /></Icon>
                    <Text>{returningInformationState(interval,'0')} min</Text>
                </IconTextContainer>

                <IconTextContainer>
                    <Icon><TrafficLight /></Icon>
                    <Text>{returningInformationState(statusCode,'Inactiva')} / {returningInformationState(statusReason,'En revision')}</Text>
                </IconTextContainer>
            </RouteInfo>
        </RouteInfoContainer>
                

    )
}
    

const mapRootStateToProps = state=>{
    return {
        route:state.RouteReducer.route
    }
}


export default connect(mapRootStateToProps)(RouteInfoSection)

