import React from 'react'
import { LeftSection,RightSection,Name,Icon,
  TimeFrameContainer,MiniLetters,Time,MainContainer } from './Feedback_styled'


function Feedback({feedbackInfo,children}) {
  
  return (
    <MainContainer>
      <LeftSection>
        <Name>Ana Bell</Name>
        <Icon> {children} </Icon>
      </LeftSection>

      <RightSection>
        <TimeFrameContainer>
          <div style={{display:'flex',alignItems:'flex-start'}}>
            <p style={{width:'100%'}}>“Ese maldito chofer tiene que dale ma suave.”</p>
          </div>

          <div style={{position:'absolute',bottom:'0'}}>
            <MiniLetters>- Hace</MiniLetters>
            <Time>12 horas</Time>
          </div>
          
        </TimeFrameContainer>
      </RightSection>
    </MainContainer>
  )
}

export default Feedback
