import React from 'react';
import { connect } from 'react-redux';
import { onFormTabClick,onRouteTabClick } from 'Redux/GeneralReducer'
import { CustomFormButton,CustomRouteButton,TabContainer } from './FormRouteTabs_styled'

function  FormRouteTabs (props){
  const { FormTabClick,RouteTabClick } = props;
  
  const isAdmin = props.states.UserReducer.isAdmin;
  return (
    <TabContainer className="">
        <CustomFormButton onClick={FormTabClick}>Formulario</CustomFormButton>
        <CustomRouteButton onClick={RouteTabClick}>{isAdmin?'Usuarios':'Rutas'}</CustomRouteButton>
    </TabContainer>
  )

}


const MapRootStateToProps = (states)=>{
  return {
    states:states
  }
}

const MapDispatchToProps = (dispatch)=>{
  return{
    FormTabClick:()=>{dispatch(onFormTabClick())},
    RouteTabClick:()=>{dispatch(onRouteTabClick())}
  }
}
export default connect(MapRootStateToProps,MapDispatchToProps)(FormRouteTabs)