import React from 'react'
import Map from '../MapTest/Map'
import { Container,MapWrapper,TopSection}  from './MapContainer_styled'
import MapTopMenu from '../MapTopMenu/MapTopMenu'
import MapControlButtons from '../MapControlButtons/MapControlButtons'

function MapContainer() {
  return (
    <Container>
      <TopSection>
        <MapControlButtons />
        <MapTopMenu/>
      </TopSection>
      
      <MapWrapper>
        <Map/>
      </MapWrapper>
    </Container>
  )
}

export default MapContainer
