import React,{useCallback} from 'react'
import { Container,HideMarkersButton,HideMarkersContainer,
  HidePolylineButton,HidePolylineContainer}  from './MapControlButtons_styled'
import { connect } from 'react-redux'
import { toggleMarkersVisibility, togglePolylineVisibility } from 'Redux/ReduxActions'

const buttonVariant = {
  hidden:{}
}

function MapControlButtons(props) {
  const { toggleMarkers,togglePolyline } = props;
  const { marker,map } = props
  

  const onMarkerClickHandler = useCallback(() => {
      toggleMarkers()
      map.mapLoaded && (
        map.google.maps.Marker.nd.setVisible(false)
      )},
    [],)

  return (
    <Container>
      <HideMarkersContainer>
        <HideMarkersButton
          whileTap={{scale:.9}} 
          onClick={onMarkerClickHandler}>Ocultar Paradas</HideMarkersButton>
      </HideMarkersContainer>

      <HidePolylineContainer>
        <HidePolylineButton 
          whileTap={{scale:.9}} 
          onClick={togglePolyline}>Ocultar Ruta</HidePolylineButton>
      </HidePolylineContainer>
    </Container>
  )
}
const mapStateToProps = (state)=>{
  return{
    marker:state.MarkersReducer,
    map:state.MapReducer
  }
}
const mapDispatchToProps = (dispatch)=>{
  return{
    toggleMarkers: ()=>{dispatch( toggleMarkersVisibility() )},
    togglePolyline: ()=>{dispatch(togglePolylineVisibility() )},
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(MapControlButtons)
