import React from 'react'
import { HeaderContainer,Header,HeaderInfo,InputWrapper,
    InputContainer,InputLabel,Input,TextAreaWrapper,TextArea,
    TextAreaContainer,TextAreaLabel,Image,Container,BackgroundImage,FloatButton } from './Contact_styled'
    
import {ReactComponent as ContactUsImage} from 'Images/contact-us.svg';
import {ReactComponent as Wave1 } from 'Images/wave1.svg';
import { useState } from 'react';
import { sendFeedback } from '../../API';

function Contact() {

  const [contactData,setContactData] = useState({
    userID:'',
    name:'',
    lastName:'',
    email:'',
    message:''})

  const handleChange = (evt)=>{
    const name = evt.target.name;
    const value = evt.target.value;

    setContactData(
      {
      ...contactData,
      [name]:value
    })
  }


  return (
    <Container >
      <Image><ContactUsImage/> </Image>
      <HeaderContainer>
        <Header>Contactanos</Header>
        <HeaderInfo>
          Lorem ipsum dolor sit amet, 
          consectetur adipiscing elit ut aliquam
        </HeaderInfo>
      </HeaderContainer>

      <InputWrapper>
        <InputContainer>
          <InputLabel>Correo</InputLabel>
          <Input name="email" value={contactData.email} onChange={handleChange}/>
        </InputContainer>

        <InputContainer>
          <InputLabel>Nombre</InputLabel>
          <Input name="name" value={contactData.name} onChange={handleChange}/>
        </InputContainer>

        <InputContainer>
          <InputLabel>Apellido</InputLabel>
          <Input name="lastName" value={contactData.lastName} onChange={handleChange}/>
        </InputContainer>
      </InputWrapper>

      <TextAreaWrapper>
        <TextAreaContainer>
          <TextAreaLabel>Mensaje</TextAreaLabel>
          <TextArea name="message" value={contactData.message} onChange={handleChange}/>
        </TextAreaContainer>
      </TextAreaWrapper>

      <BackgroundImage>
        <Wave1 />
      </BackgroundImage>
      <FloatButton onClick={()=>{
        sendFeedback(contactData).then(res=>{console.log(res)}).catch(err=>{console.log(err)})
        }}>Enviar</FloatButton>
    </Container>
  )
}

export default Contact


