import React from 'react';
import { CustomIcon , CustomInput, InputImageContainer} from './CustomizeInput_styled'

function CustomizeInput({_icon,_function,inputData}){
  const {placeholder,type,name,value } = inputData
  
  return(
    <InputImageContainer>
      <CustomIcon src={_icon}></CustomIcon>
      
      <CustomInput 
        placeholder={placeholder} 
        type={type}
        name={name}
        value={value}
        onChange={_function}
        
      />
    </InputImageContainer>
  )  
}


export default CustomizeInput
