import React from 'react'
import styled from 'styled-components'

export const NotFoundContainer = styled.div`
    height: 100vh;

    display:flex;
    justify-content:center;
    align-items:center;
`
export const Message = styled.p`
    padding:2rem;
    text-align:center;
    font-size:2rem;
`

function NoRoutesFound() {
  return (
    <NotFoundContainer>
      <Message>
          No posee ninguna ruta por el momento
      </Message>
    </NotFoundContainer>
  )
}

export default NoRoutesFound
