import React from 'react'
import { LinkWrapper,Title,Input,Icon,Button } from './EditSocialLinks_styled'

function EditSocialLinks({children}) {
  return (
      <LinkWrapper >
        <Icon > {children} </Icon>
        <Title>Link</Title>
        <Input />
        <Button> <span>Guardar</span></Button>
      </LinkWrapper>
  )
}

export default EditSocialLinks
