import {  unloadMarkers,unloadRoute,unloadPolyline,
          resetGeneralSettings,fillRoute,fillingMarker,fillingPolyline,
          changePostToPut,deleteRoutePosition,DeletePolyline,deleteMarker,
          ChangeGeneralTransportType,onChangeRouteTransportType,getGeocodeAndSavingRoutePosition,
          addingMaker,addingPolyline,getRoutes } from './ReduxActions';
import { unloadMap } from './MapReducer';
import { unloadRoutes } from './RoutesReducer';
import { unloadLogging } from './Login';
import { unloadUser } from './UserReducer'



export const unloadApp = ()=>{
  return ((dispatch)=>{
    dispatch(unloadMarkers());
    dispatch(unloadRoute());
    dispatch(unloadPolyline());
    dispatch(resetGeneralSettings());
  })
}

export const resetApp = ()=>{
  localStorage.clear();
  return((dispatch)=>{
    dispatch(unloadMarkers());
    dispatch(unloadRoute());
    dispatch(unloadPolyline());
    dispatch(resetGeneralSettings());
    dispatch(unloadMap());
    dispatch(unloadRoutes());
    dispatch(unloadUser());
    dispatch(unloadLogging());
  })
}

export const loadApp = (route)=>{
  const transportType = route.aditionalInfo.transportType
  return ((dispatch)=>{
    dispatch(fillRoute(route));
    dispatch(fillingMarker(route));
    dispatch(fillingPolyline(route));
    dispatch(changePostToPut())
    dispatch(ChangeGeneralTransportType(transportType))
    
  })
}

export const deleteMarkerPolylineRoutePosition = (coords)=>{
  return ((dispatch)=>{
    dispatch(deleteRoutePosition(coords))
    dispatch(deleteMarker(coords));
    //dispatch(DeletePolyline(event));
  })
}

export const addMarkerPolylineRoutePosition = (coords)=>{
  return((dispatch)=>{
    dispatch(addingMaker(coords))
    //dispatch(addingPolyline(coords))
    dispatch(getGeocodeAndSavingRoutePosition(coords));
  })
}

export const updateMarkerPolylineRoutePosition = (coords)=>{
  
}

export const buttonGroupControl = (transportType)=>{
  return ((dispatch=>{
    dispatch(onChangeRouteTransportType(transportType))
    dispatch(ChangeGeneralTransportType(transportType))
  }))
}