import { defaultLocalStorage } from "./global_constants"
const localData =  localStorage.getItem('user') 
const localUser = localData ? JSON.parse(localData) : defaultLocalStorage

//Get Local store
export const getUserInfoFromLocalStore = ()=>{

  return localUser.resolve
}

export const getUserRoleFromLocalStore = ()=>{

  return localUser.resolve.role
}

export const getUserTokenFromLocalStore = ()=>{

  return localUser.token
}

export const getLocalStorageInfo = ()=>{
  const localData =  localStorage.getItem('user') 
  const localUser = localData ? JSON.parse(localData) : defaultLocalStorage
  const { resolve:{role} } = localUser;

  const localStorageInfo = {
    user : localUser.resolve,
    userRole:localUser.resolve.role,
    token: localUser.token,
    isAdmin:()=>{
      if(role === 'ADMIN')
        return true
      else 
        return false
    },
    isAdminOrProvider:()=>{
      if(role === 'ADMIN' || role === 'PROVIDER'){
        return true
      }else{
        return false
      }
    }
  }
  return localStorageInfo
}

