import React from 'react'

import { connect } from 'react-redux';

import { Container,RouteContainer, TitleContainer, Title, 
        BackgroundImageContainer, BackgroundImage, RouteReviewTitleContainer, 
        RouteReviewTitle, IdContainer, Id } from './RouteCard_styled';


import {ReactComponent as Road} from 'Images/road-route-map.svg';

import Testing from './RouteReviewSection/RouteReviewSection';
import RouteInfoSection from './RouteInfoSection/RouteInfoSection';


const isGreaterThanCero = (text)=>{
    
    if (text.length === 0 ){
        return false
    }else{
        return true
    }  
}

function  Routes (props)  {
    const { route:{ID,routeName,schedule:{interval},
            review,position,status:{statusCode,statusReason} } } = props;

    const returningInformationState = (state,customText)=>{
        return isGreaterThanCero(state) ? state : customText
    }

    return (
        <Container>
            <RouteContainer>
                <BackgroundImageContainer>
                    <BackgroundImage><Road /></BackgroundImage>
                </BackgroundImageContainer>
                
                <IdContainer>
                    <Id>{returningInformationState(ID,'ID')}</Id>
                </IdContainer>

                <TitleContainer>
                    <Title>
                        {returningInformationState(routeName.substring(0,10),'Nombre Ruta')}  
                        {routeName.length > 10 && '...'}
                    </Title>
                </TitleContainer>

                <RouteInfoSection
                    position={position} interval={interval} 
                    statusCode={statusCode} statusReason={statusReason}/>
                
                <RouteReviewTitleContainer>
                    <RouteReviewTitle>Clasificacion y comentarios</RouteReviewTitle>
                </RouteReviewTitleContainer>

                <Testing review={review}/>
            </RouteContainer>
        </Container>
    )
}
    

const mapRootStateToProps = state=>{
    return {
        route:state.RouteReducer.route
    }
}


export default connect(mapRootStateToProps)(Routes)

