import * as options from './RouteTypes';

const initState = {
  routeSavingStatus:{
    error:false,
    loading:false,
    saved:false
  },
  routeInfo:{
    positionLength:0,
    newPositionLength:0
  },
  route:{
    ID: "",
    routeName: "",
    schedule:{
      startTime: "",
      endTime: "",
      interval: ""
    },
    price:"", 
    destination:"",
    aditionalInfo: {
      transportType: "",
      details: "",
      warnings: ""
    },
    review:[],
    position: [],
    active:{},
    status:{
      statusCode:'',
      statusReason:''
    },
    created:{
      createdBy:{
        ID:'',
        fullName:''
      },
      createdDate:''
    },
    record:{
      by:{ID:'',fullName:''},
      timeStamp:''
    },
  }
}


const RouteReducer = (state = initState, action) =>{
  switch (action.type) {
    case options.ROUTE_LOADING:
      return{
        ...state,
        routeSavingStatus:{
          error:false,
          loading:true,
          saved:false
        }
      }
    case options.ROUTE_ERROR:
      return{
        ...state,
        routeSavingStatus:{
          error:true,
          loading:false,
          saved:false
        }
      }
    case options.ROUTE_SUCCESS:
      return{
        ...state,
        routeSavingStatus:{
          error:false,
          loading:false,
          saved:true
        }
      }
    case options.ROUTE_SAVINGSTATUS_RESET:
      return {
        ...state,
        routeSavingStatus:initState.routeSavingStatus
      }
      
    case options.UPDATE_POSITION:
      const { 
          lat:updated_lat,lng:updated_lng,
          currentPosition:{lat:current_lat,lng:current_lng},
          street_name:streetName
      } = action.refData;
      
      return {
        ...state,
        route:{
          ...state.route,
          position:state.route.position.map((coords,index)=>{  
            if(current_lat === coords.LatLng[0]){
              if(current_lng === coords.LatLng[1]){
                return {
                  ...coords,
                  LatLng:[updated_lat,updated_lng],
                  streetName:streetName                    
                }
              }else{
                return coords
              }   
            }else{
              return coords
            }
          })
        }
      }
    case options.UNLOAD_ROUTE:
      return initState
    case options.FILL_ROUTE:
      const route = action.route
      return {
        ...state,
        routeInfo:{
          ...state.routeInfo,
          positionLength:route.position.length,
          newPositionLength:route.position.length
        },
        route:route
      }

    case options.CHANGE_ROUTE_TRANSPORT_TYPE:
      const transportType = action.transportType;
      return{
        ...state,
        route:{
          ...state.route,
          aditionalInfo:{
            ...state.route.aditionalInfo,
            transportType:transportType
          }
        }
      }
    case options.ADD_POSITION:
      const { lat,lng, street_name } = action.routePositionInfo;
      return{
        ...state,
        routeInfo:{
          ...state.routeInfo,
          newPositionLength:state.routeInfo.newPositionLength + 1
        },
        route:{   
          ...state.route,
          position:[
            ...state.route.position,
            {
              LatLng:[lat,lng],
              streetName:street_name
            }
          ]
        }
      }
    case options.DELETE_POSITION:
      let coordinatesToDelete = action.payload[0];
      return{
        ...state,
        routeInfo:{
          ...state.routeInfo,
          newPositionLength:state.route.position.length -1
        },
        route:{
          ...state.route,
          position:[ 
            ...state.route.position.filter((coords,index)=>{
              // eslint-disable-next-line
              if(coordinatesToDelete == coords.LatLng[0]){
                return !coords
              }else{
                return coords
              }
            })
          ]
        }
        
      }
    case options.ROUTE_CHANGE:
      const { event } = action;
      
      const route_name = event.target.name;
      const input_value = event.target.value;
      console.log(state.route)
      
      
      switch (route_name) {
        case 'ID':
          return {
            ...state,
            route:{
              ...state.route,
              ID:input_value
            }
          }
        case 'routeName':
          return{
            ...state,
            route:{
              ...state.route,
              routeName:input_value
            }
          }
        case 'price':
          if(isNaN(input_value)){
            return {
              ...state,
              route:{
                ...state.route,
                price:state.route.price
              }
            }
          }else{

            return{
              ...state,
              route:{
                ...state.route,
                price: `${input_value}`
              }
            }
          }
          

        case 'destination':
          return{
            ...state,
            route:{
              ...state.route,
              destination:input_value
            }
          }
        case 'startTime':
          return{
            ...state,
            route:{
              ...state.route,
              schedule:{
                ...state.route.schedule,
                startTime:input_value
              }
            }
          }
          case 'endTime':
          return{
            ...state,
            route:{
              ...state.route,
              schedule:{
                ...state.route.schedule,
                endTime:input_value
              }
            }
          }
          case 'interval': 
          if(isNaN(input_value)){
            return {
              ...state,
              route:{
              ...state.route,
              schedule:{
                ...state.route.schedule,
                interval:state.route.schedule.interval
              }
            }}
          }else{
            return{
              ...state,
              route:{
                ...state.route,
                schedule:{
                  ...state.route.schedule,
                  interval:input_value
                }
              }
            }
          }
          case 'details':
          return{
            ...state,
            route:{
              ...state.route,
              aditionalInfo:{
                ...state.route.aditionalInfo,
                details:input_value
              }
            }
          }
          case 'warnings':
          return{
            ...state,
            route:{
              ...state.route,
              aditionalInfo:{
                ...state.route.aditionalInfo,
                warnings:input_value
              }
            }
          }
        default:
          return state.route;
      }/*
      return{
        ...state,
        route:
          {
            ...state.route,
            [route_name]:input_value,
            schedule: {
              ...state.route.schedule,
              [route_name]:input_value
            },
            aditionalInfo:{
              ...state.route.aditionalInfo,
              [route_name]:input_value
            }
          }
      }*/
    default:
      return state;
  }
  
}

export {RouteReducer};