import styled from 'styled-components'
import { motion } from 'framer-motion';

export const Container = styled(motion.div)`
  background-color:#393e46;
  min-width:15rem;
  min-height:10rem;
  border-radius: 10px;
  position:absolute;
  top: 4rem;
  right: .98rem;
  z-index: 2;
  max-height:10rem;
  overflow:scroll;

  ::-webkit-scrollbar{
    display: none;
  }

  //animation: scale-in-ver-top 400ms;
  
`;

export const StopsContainer = styled(motion.div)`
  position:relative;
  background: #F7F7F7;
  border-radius: 10px;
  min-width:13rem;
  display: flex;

  cursor:pointer;
  

  padding:.5rem 1.2rem;
  margin-bottom:.2rem;
`

export const StopInfo = styled.span`

`;

export const StopDeleteIconContainer = styled.div`
  position: absolute;
  right: 1rem;

  svg{
    cursor:pointer;
    transition:300ms;

    fill: rgba(231, 0, 0, 0.65);
    :hover{
      fill: rgba(231, 0, 0, 1);
    }
  }
`;

export const StopWrapper = styled.div`
  padding:1rem;
`;

export const NoStopsContainer = styled.div`
  background-color:#393e46;
  min-width:15rem;
  height:15rem;
  z-index: 2;
  border-radius: 10px;
  position:absolute;
  top: 0;
  right: 0;
  z-index: 2;
  max-height:10rem;
  overflow:scroll;

  ::-webkit-scrollbar{
    display: none;
  }
  display:flex;
  justify-content:center;
  align-items:center;
`;
export const NoStopsMessage = styled.p`
  text-align:center;
  color:white;
`;