import React from 'react'
import { connect } from 'react-redux';

import { useHistory, Link, useRouteMatch  } from 'react-router-dom'
import { DropDown,DropDownItem} from './NavbarDropDown_styled'
import { resetApp } from 'Redux/CombinedActions';

function NavbarDropDown ({hidden=false,unloadApp}){
  const history = useHistory();
  const {path} = useRouteMatch();
  const logOut = ()=>{
    history.push('/');
    unloadApp();
  }
 

  return(
    <DropDown style={hidden?{display:'none'}:{display:'block'}} className="testing">
      <Link to={`${path}/contactus`}> 
        <DropDownItem >Contactanos</DropDownItem>
      </Link>
      <Link to={`${path}/user`}>
        <DropDownItem>Configuracion</DropDownItem>
      </Link>
      <DropDownItem onClick={logOut}>Salir</DropDownItem>
    </DropDown>
  )
}

const mapDispatchToProps = (dispatch)=>{
  return{
    unloadApp: ()=>{dispatch(resetApp())},
  }
}


export default connect (null,mapDispatchToProps) (NavbarDropDown)
