
import React, { useCallback } from 'react';

import { connect } from 'react-redux';
import { MouseIn , MouseOut, toggleConfirmationMessage } from 'Redux/GeneralReducer'

import { loadApp } from 'Redux/CombinedActions'
import { panToHandler, switchClicked, saveCurrentPosition } from 'Redux/ReduxActions';

import {RouteName,RouteNameContainer,BackgroundImage,DetailContainer} from './RouteDetailContainer_styled'
import LeftSideSection from './LeftSideSection/LeftSideSection';
import RightSideSection from './RightSideSection/RightSideSection';
import { ReactComponent as Bus } from 'Images/bus.svg'



const getCentraLocation = (routeInfo)=>{
    const centralRoutePositionIndex = Math.round(routeInfo.position.length/2);
    const centralPosition =routeInfo.position[centralRoutePositionIndex];
    
    const lat = centralPosition.LatLng[0]
    const lng = centralPosition.LatLng[1]

    return {lat,lng}
}

function RouteDetailContainer (props) {
    const { ID,routeName } = props.routeInfo;
   
    const { onMouseHover,onMouseOut,fillingApp,map:{mapRef,mapLoaded,clicked,currentPosition},
            switchClicked,currentPositionHandler } = props;

    const handleClick = useCallback( (evt)=>{
        evt.stopPropagation();
        fillingApp(props.routeInfo)
        currentPositionHandler(getCentraLocation(props.routeInfo))
        switchClicked(true)
    },[])

    
    const handleOnPointerEnter = useCallback( (evt)=>{
        evt.stopPropagation();
        onMouseHover(props.routeInfo);
        const {lat,lng} = getCentraLocation(props.routeInfo)

        panToHandler(mapRef,{lat,lng,zoom:13.8})
    },[])

    const handleOnPointerLeave =  (evt)=>{
        evt.stopPropagation();
        onMouseOut()
        
        if(clicked === true && mapLoaded === true ){
            panToHandler(mapRef,currentPosition)
        }
    }
    
    return (
        <DetailContainer 
            onClick={handleClick}
            onPointerEnter={handleOnPointerEnter}
            onPointerLeave={handleOnPointerLeave}
        >   
        <RouteNameContainer>
            <RouteName>{ID} / {routeName}</RouteName>
        </RouteNameContainer>
        <div style={{display:'flex'}}>
            <LeftSideSection routeInfo={props.routeInfo}/>
            <RightSideSection routeInfo={props.routeInfo}/>
        </div>
        <BackgroundImage>
            <Bus />
        </BackgroundImage>

        </DetailContainer>
    )
}


const mapRootStateToProps = state=>{
    return {
        map:state.MapReducer
    }
}

const mapDispatchToProps = (dispatch)=>{
    return{
        onMouseHover: (route)=>{dispatch(MouseIn(route))},
        onMouseOut: ()=>{dispatch(MouseOut())},
        fillingApp: (route)=>{dispatch(loadApp(route))},
        switchConfirmationMessage: (payload)=>{dispatch(toggleConfirmationMessage(payload))},
        switchClicked:(bool)=>{dispatch(switchClicked(bool))},
        currentPositionHandler:(LatLng)=>{dispatch(saveCurrentPosition(LatLng))}
    }
}



export default connect(mapRootStateToProps,mapDispatchToProps)(RouteDetailContainer)


