import React, { Fragment,useState } from 'react'

import { Marker,InfoWindow } from '@react-google-maps/api'
import { useCallback } from 'react';
import { connect } from 'react-redux';
import { markerDragEnd } from 'Redux/AsyncActions';

function MarkerInfoWindow({position,icon,animation,map,allVisible,routeState,onDragend}) {
  const [open,setOpen] = useState(false);

  const onClickHandler = useCallback(()=>{
    setOpen(!open)
  },[open])

  const minimizeMarkers = ()=>{
    return position
  }
  return(
    <Fragment>
      {allVisible && 
        <Fragment>
          <Marker 
            position={minimizeMarkers()}
            draggable={true}
            icon={icon}
            onClick={onClickHandler}
            onDragEnd={(markerProps, map, clickEvent)=>{
            
            const lat = markerProps.latLng.lat()
            const lng = markerProps.latLng.lng()

            //position;

            onDragend({lat, lng, position})
          }}
          />

          {open && 
            <InfoWindow 
              onCloseClick={onClickHandler}
              position={position}
              >
              <div > 
                <h2>Some Data</h2> 
                <p>Lrem ipsuom data simadi</p> 
              </div>
            </InfoWindow>
          }
        </Fragment>

      }
        
    </Fragment>
  )
}

const mapRootStateToProps = state=>{
  return {
    route:state.RouteReducer.route,
    marker:state.MarkersReducer,
    polyline:state.PolylineReducer,
    mapState:state.MapReducer,
    general:state.GeneralReducer
  }
}


const mapDispatchToProps = (dispatch)=>{
  return{
    onDragend: (markerProps)=>{dispatch(markerDragEnd(markerProps))}
  }
}

export default connect(mapRootStateToProps,mapDispatchToProps)( MarkerInfoWindow )


