
import React from 'react';
import {Container,LeftContainerWrapper} from './LeftContainer_styled';

import Route from '../Routes/Route'
import FormRouteTabs from '../FormRouteTabs/FormRouteTabs';

import { connect } from 'react-redux';
import  Form  from 'Components/Form/Form';

import { ThemeProvider } from 'styled-components'
import UserList from '../UserList/UserList'

function LeftContainer (props)  {
    const theme = {
        generalState:props.states.GeneralReducer.generalState
    }

    const { routes } = props.states.RoutesReducer;
    const { generalState }  = props.states.GeneralReducer;
    const isAdmin = props.states.UserReducer.isAdmin;

    return (
    <ThemeProvider theme={theme}>
        <LeftContainerWrapper>
            <Container >
                <FormRouteTabs ></FormRouteTabs>
                {generalState.tabs === 'form'
                    //*if
                    ? <Form /> 
                    //*else
                    : isAdmin ? <UserList /> : <Route routes={routes}/> 
                }
            </Container>
        </LeftContainerWrapper>
    </ThemeProvider>
    )
}


const mapRootStateToProps = state=>{
    return {
        states:state
    }
}



export default connect(mapRootStateToProps) ( LeftContainer )