


export const getRouteList = (routeState)=>{
    const routeList = { 
    ID:{
        label:'ID Ruta',name:'ID',
        value:routeState.ID,
        size:10,
        pattern:'',
        placeholder:"115B"
    },
        routeName:{
        label:'Nombre Ruta',name:'routeName',
        value:routeState.routeName,
        size:30,
        placeholder:"Charles 9"
    },
    price:{
        label:'Precio',name:'price',
        value:routeState.price,
        min:0,
        max:1000,
        size:5,
        pattern:/[\D\s\._\-]+/g,
        placeholder:"25",
        price:true,
        type:'text'
    },
    interval:{
        label:'Intervalo',name:'interval',
        value:routeState.schedule.interval,
        pattern:/^([0-1]\d|20|21|22|23):[0-5]\d$/,
        size:6,
        type:'text',
        placeholder:"15",
    },
    destination:{
        label:'Destino',name:'destination',
        placeholder:"Destino",
        value:routeState.destination,
        max:30,
    },
    startTime:{
        label:'Hora Ini',name:'startTime',
        value:routeState.schedule.startTime,
        type:'time'
    },
    endTime:{
        label:'Hora Fin',name:'endTime',
        value:routeState.schedule.endTime,
        type:'time'
    },
    details:{
        label:'Detalles',
        name:'details',
        value:routeState.aditionalInfo.details,
        placeholder:'Detalles',
        type:'textArea',
        size:100,
    },
    warnings:{
        label:'Advertencia',
        name:'warnings',
        value:routeState.aditionalInfo.warnings,
        placeholder:'Advertencias',
        type:'textArea',
        size:100,
    }
}
    return routeList
}