import React,{useEffect} from 'react';

import { Container,HeaderContainer,Header3,TableContainer,
    TableBody,TableHead,TableHeader,TableRow,DTable} from './Table_styled'
import TableInfo from './Components/TableInfo';
import { connect } from 'react-redux'
import { getAllUsers } from 'Redux/ReduxActions';


function Table(props) {
  const { loginReducer,getAllUsers } = props;
  const { user:{isAdmin} } = props;

  useEffect(()=>{
    isAdmin && getAllUsers();
  },[isAdmin,getAllUsers])
  
  const { users=[] } = loginReducer.user;

  return (
    <div>
      {isAdmin && 
        <Container>
          <HeaderContainer>
            <Header3>Cuidaooooo con el fuego</Header3>
          </HeaderContainer>
          
          <TableContainer>
            <DTable>
              <TableHead>
                <TableRow>
                  <TableHeader>
                    <input type="checkbox" name="" id=""/>
                  </TableHeader>
                  <TableHeader>Nombre</TableHeader>
                  <TableHeader>Apellido</TableHeader>
                  <TableHeader>Correo</TableHeader>  
                  <TableHeader>Rol</TableHeader>    
                </TableRow>
              </TableHead>
              
              <TableBody>
                {users.map((data,index)=>{
                    return <TableInfo key={index+5} data={data}/>
                  })}  
              </TableBody>

            </DTable>
          </TableContainer>
          
        </Container>
      }
    </div>
  )
}

const mapRootStateToProps = state=>{
  return {
    loginReducer:state.LoginReducer,
    user: state.UserReducer
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
    getAllUsers:()=>{dispatch(getAllUsers())}
  }
}

export default connect(mapRootStateToProps,mapDispatchToProps)(Table)
