import styled from 'styled-components'
import { motion } from 'framer-motion';


export const ListLogContainer = styled(motion.div)`
  overflow:hidden;
  position:relative;
  display:flex;
  justify-content:center;
  margin:1rem 0;
  cursor:pointer;
  
`;
export const ListLog = styled.div`
  margin:.2rem 0;

  p{
    display:inline-block;
    margin-left:1rem;
  }
`;

export const LogDate = styled.span`
  font-weight:500;
  width:30%;
  display: inline-block;
  text-align:left;
  margin-left:1rem;
`;

export const LogTime = styled.span`
  width:20%;
  font-weight:500;
  display: inline-block;
`;

export const LogText= styled.span`
  width:15%;
  font-weight:500;
  display: inline-block;
  margin-left:1rem;
`;

export const LogIcon= styled.div`
  display:inline-block;
  margin:0 .5rem;
  width:10%;

  svg{
    display:inline-block;
    height: 1.5rem;
    width: 1.5rem;
  }
`;

export const LogBackground= styled.div`
  

  svg{
    position:absolute;
    top:0rem;
    fill:gray;
    right:-1.5rem;
    opacity:0.2;
    transform:rotate(105deg);
    height: 5rem;
    width: 5rem;
  }
`;

