import React from 'react'
import {Container,CentralBar,Section,Copyright } from './Footer_styled'
import FooterItem from './FooterItem/FooterItem';

function Footer_styled() {
  return (
    <Container>
      <CentralBar>
        <Section>
          <Copyright>	&copy; 2020 Hecho por... </Copyright>
        </Section>
        <Section>
          <FooterItem text='Twitter'/>
          <FooterItem text='Facebook'/>
          <FooterItem text='Instagram'/>
          <FooterItem text='Contactanos'/>
        </Section>
      </CentralBar>
    </Container>
  )
}

export default Footer_styled
