import React, { useRef } from 'react'

import { GoogleMap,Polyline,LoadScript } from '@react-google-maps/api'
import { connect } from 'react-redux';
import { getRoutes, OnMapClick, markerDragEnd, testing, loggingUser, saveMapInfo,panToHandler } from 'Redux/ReduxActions';
import { useCallback } from 'react';

import { useEffect } from 'react';
import SearchContainer  from './SearchContainer';
import MarkerInfoWindow from './Marker&InfoWindow';


const containerStyle = {
  width: '100%',
  height: '100%'
};

const libraries = ['places']

function Map(props) {
  const { mapState,marker:{markersArray:markers=[],icon,allVisible} } = props;
  const { polyline:{polylineCoordinates,options} } = props;
  const { general:{routeState,hoveredPolylineCoordinates} } = props;
  const { saveMap,onMapClick } = props;

  const { mapState:{google,mapRef:mapReference,mapLoaded} } = props;

 
  const mapRef = useRef();

  const mapClickHandler = useCallback( (clickEvent,e)=>{
    const lat = clickEvent.latLng.lat();
    const lng = clickEvent.latLng.lng();
    onMapClick({lat,lng})
  },[])

 
  const onMapLoad = useCallback(map=>{
    const mapObject =window.google;
    mapRef.current = map;

    saveMap({mapRef,mapObject})

  },[])


  const panTo = React.useCallback(({ lat, lng }) => {
    panToHandler(mapRef,{lat,lng})
    //mapRef.current.panTo({ lat, lng });
    //mapRef.current.setZoom(14);
  }, []);
  useEffect(()=>{

  },[])
  
  return (
    <LoadScript
      googleMapsApiKey={process.env.REACT_APP_GOOGLE_API}
      libraries={libraries}
    >
    <SearchContainer panTo={panTo}></SearchContainer>
      <GoogleMap
        mapContainerStyle={containerStyle}
        center={mapState.initial_center}
        zoom={mapState.zoom}
        onClick={mapClickHandler}        
        options={
          { styles:mapState.styles,
            streetViewControl:false,
            fullscreenControl:false,
            mapTypeControl: false}
        }
        clickableIcons={false}
        onLoad={onMapLoad}
        language={`es`}
      >
    

      { markers.map( (marker,index)=>
        <MarkerInfoWindow 
          routeState={routeState}
          allVisible={allVisible}
          map={mapRef}
          key={`marker-${index}`} 
          position={marker.position} 
          icon={icon}/> )
      }

      <Polyline 
        path={routeState==='normal'?polylineCoordinates:hoveredPolylineCoordinates}
        options={options}
        />
  
      </GoogleMap>
    </LoadScript>
  )
}


const mapRootStateToProps = state=>{
  return {
    route:state.RouteReducer.route,
    marker:state.MarkersReducer,
    polyline:state.PolylineReducer,
    mapState:state.MapReducer,
    general:state.GeneralReducer
  }
}


const mapDispatchToProps = (dispatch)=>{
  return{
    fillRoute: ()=>{dispatch(getRoutes())},
    onMapClick: (mapProps)=>{dispatch(OnMapClick(mapProps))},
    onDragend: (markerProps)=>{dispatch(markerDragEnd(markerProps))},
    testing : (googleProps)=>{dispatch(testing(googleProps))},
    fillUser : (userData)=>{dispatch(loggingUser(userData))},
    saveMap : (mapInfo)=>{dispatch(saveMapInfo(mapInfo))}
  }
}

export default connect(mapRootStateToProps,mapDispatchToProps)(Map)




