import React from 'react'
import { Container,Image,InfoContainer,InfoSvg,InfoMessage, BackgroundImage } from './Direction_styled'

import {ReactComponent as Phone} from 'Images/phone.svg';
import {ReactComponent as Email} from 'Images/mail.svg';

import {ReactComponent as Location } from 'Images/location.svg';
import {ReactComponent as Map } from 'Images/map.svg';
import {ReactComponent as Wave2 } from 'Images/wave2.svg';

function Direction() {
  return (
    <Container> 
      <Image> <Map /> </Image>
      <InfoContainer>
        <InfoSvg> <Location /> </InfoSvg>
        <InfoMessage>THE TRADE CENTER BUSINESS COMPLEX, SUITE 3A, 30-32 RED HILLS RD, KINGSTON 10, JAMAICA.</InfoMessage>
      </InfoContainer>

      <InfoContainer>
        <InfoSvg> <Phone /> </InfoSvg>
        <InfoMessage>+1 876 960 1004</InfoMessage>
      </InfoContainer>

      <InfoContainer>
        <InfoSvg> <Email /> </InfoSvg>
        <InfoMessage>info@axlejm.com</InfoMessage>
      </InfoContainer>

      <BackgroundImage>
        <Wave2 />
      </BackgroundImage>
    </Container>
  )
}

export default Direction
