import React from 'react'
import {ListLogContainer,ListLog,LogBackground,LogDate,LogIcon,LogText,LogTime } from './Log_styled';
import { ReactComponent as Login } from 'Images/login.svg';

import { ReactComponent as LogOut } from 'Images/logout.svg';
import { ReactComponent as  Background } from 'Images/api.svg';

function Log() {
  return (
    <ListLogContainer 
      whileHover={{scale:1.05}}>
      <div style={{width:'100%'}}>
        <ListLog>
          <LogIcon> <Login style={{fill:'#08d9d6'}}/> </LogIcon>
          <LogDate>2 Meses</LogDate>
          <LogText> 12:00 </LogText>
          <LogTime>  AM</LogTime>
        </ListLog>

        <ListLog>
          <LogIcon> <LogOut style={{fill:'#ff2e63'}}/> </LogIcon>
          <LogDate> 2 Meses </LogDate>
          <LogText> 4:35 </LogText>
          <LogTime>  PM</LogTime>
        </ListLog>
      </div>
      <LogBackground> <Background /> </LogBackground>
    </ListLogContainer>
  )
}

export default Log
