
import bddHelper from '../../BDD/Request'
import { AddingRoutePosition, getGeolocationAndUpdatingRoutePosition } from '../RouteReducer';
import { updatingMarker,updatingRoutePosition } from '../ReduxActions';

export const getGeocodeAndSavingRoutePosition = (something)=>{
  const { lat,lng} = something;
  return((dispatch)=>{
    bddHelper.getGeocodeRequest(lat,lng)
    .then((street_name)=>{
      dispatch( AddingRoutePosition({lat,lng,street_name}) )
    })
    .catch((err)=>{
      console.log(err)
    })
  })
}

export const markerDragEnd = ({lat,lng,position:currentPosition})=>{
  
  return (dispatch)=>{
    dispatch(updatingMarker({lat,lng,currentPosition}));
    //dispatch(updatingPolyline({lat,lng,currentPosition}));
    dispatch(getGeolocationAndUpdatingRoutePosition({lat,lng,currentPosition}));
  }
}
