import React from 'react'
import { NameInitialsContainer,NameInitial } from './NameInitials_styled'


function NameInitials({onClickHandler,name,lastName}){
  return(
    <NameInitialsContainer onClick={onClickHandler}>
      <NameInitial>{name.substring(0,1)}</NameInitial>
      <NameInitial>{lastName.substring(0,1)}</NameInitial>
    </NameInitialsContainer>
  )
}

export default NameInitials
