import React from 'react'
import { RouteReviewContainer,RouteComments,RouteReview,RouteReviewIcon,RouteReviewText,LineDivider,StarBackgroundContainer,StarBackground } from './RouteReviewSection_styled'

import Rating from 'Components/Rating/Rating';


const calculateAverage = (review=[])=>{
  let counter = 0;
  let result = 0;


  review.map((comment)=>{
    counter++;
    result = result + Number( comment.raiting );

    if(review.length === counter ){
        result = (result / counter)
    }
  })
  
  return result
}

function RouteReviewSection({review=[]}) {
  const reviewAverage = calculateAverage(review).toString()

  
  return (
    <RouteReviewContainer>  
        <RouteReview>
            <RouteReviewText>{reviewAverage.substr(0,3)}</RouteReviewText>
            <Rating punctuation={reviewAverage}/>
        </RouteReview>

        <LineDivider />

        <RouteComments>
            <RouteReviewText>{review.length}</RouteReviewText>
            <RouteReviewIcon>Rese&ntilde;as</RouteReviewIcon>
        </RouteComments>

    </RouteReviewContainer>
  )
}

export default RouteReviewSection

