import React from 'react'

import { connect  } from 'react-redux'
import { handleInputChange } from 'Redux/RouteReducer';
import {InputContainer,CustomTextArea,CustomInput,CustomLabel,CustomIcon} from './CustomInputForm_styled'

const inputStyle = "shadow appearance-none border rounded py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
const labelStyle = "block text-gray-700 text-lg font-bold mb-2";


const textAreaAdjust = (evt)=> {
  console.log(evt)
}

function CustomInputForm(props){
  const { inputData = {},onRouteChange,style={},containerStyle={} } = props;
  const { type="text",size=45,name="",value="",
          required=false,placeholder="Empty",
          label="Missing Info",min=0,max=145, price=false} = inputData;
          

  return(
    <InputContainer>
      <CustomLabel className={labelStyle} style={containerStyle}>{label}</CustomLabel>
      <div style={{position:'relative'}}>

        <CustomIcon style={price?{display:'block'}:{display:'none'}}>
          $
        </CustomIcon>
        {type==='textArea'
          ?<CustomTextArea 
            placeholder={placeholder}
            className={inputStyle} 
            maxLength={size}
            name={name}
            value={value}
            onChange={onRouteChange}
            onKeyPress={textAreaAdjust.bind(this)}/>
          :<CustomInput 
            style={price?{paddingLeft:'1.5rem',width:'9rem'}:style}          
            type={type}
            className={inputStyle} 
            onChange={onRouteChange}
            name={name}
            value={value}
            required={required}
            placeholder={placeholder}
            min={min}
            max={max}
            maxLength={size}
          />}
      </div>
    </InputContainer>
  )
}

const mapRootStateToProps = (states)=>{
  return {
      states:states
  } 
}
const mapDispatchToProps = (dispatch)=>{
  return{
      onRouteChange: (event)=>{dispatch(handleInputChange(event))}
  }
}

export default connect(mapRootStateToProps,mapDispatchToProps)(CustomInputForm)
