
import styled from 'styled-components'

export const Container = styled.div`
  display:flex;
`;
export const SectionWrapper = styled.div`
  display:flex;
  margin:1rem 1rem 1rem;
  padding:1rem;
  border-radius:20px;
  box-shadow:hsla(0, 0%, 15%, 0.1) 0px 1px 15px 1px;
`;

export const SectionBottomWrapper = styled.div`
  display:flex;
  justify-content:space-between;
  margin:1rem;
  
  padding:1rem;
  border-radius:20px;
  box-shadow:hsla(0, 0%, 15%, 0.1) 0px 1px 15px 1px;
`;

export const ProfilePictureContainer = styled.div`
  
  display:flex;
  margin-right:2rem;

  
`;

export const ProfilePicture = styled.div`
  display:flex;
  justify-content:center;
  align-items:center;
  text-transform:uppercase;
  border-radius:50%;
  
  width: 5rem;
  height: 5rem;

  padding:1rem;
  background-color:rgb(245, 176, 34);

  p{
    font-size:1.5rem;
    
    font-weight:500;
  }
`;

export const PersonalInfoContainer = styled.div`
  position:relative;
`
export const PersonalInfo = styled.div`
  display:flex;
  justify-content:flex-end;
  align-items:center;
  font-weight:500;

  p{
    font-size:1.3rem;
  }
`;

export const Role = styled.div`
  display:flex;
  justify-content:center;
  align-items:center;
  width: 5rem;
  padding: .1rem .5rem;
  color:green;

  background-color:hsl(0, 0%, 100%);
  border-radius:10px;
  box-shadow:hsla(0, 0%, 15%, 0.1) 0px 1px 15px 1px;
`
export const Circle = styled.div`
  height: .9rem;
  width: 1rem;
  background-color:green;
  border-radius:50px;
  margin-right:.4rem;

`

export const RoleState = styled.div`
  display:flex;
  justify-content:center;
  align-items:center;
  font-size:.9rem;
  padding: 0 .6rem;
  margin-left:.5rem;
  color:white;

  height:1.2rem ;
 

  background-color:hsl(0, 0%, 50%);
  border-radius:20px;
  box-shadow:hsla(0, 0%, 15%, 0.1) 0px 1px 15px 1px;
`;



export const AssociationInformationContainer = styled.div`
  margin-left:4rem;
`
export const AssociationInformation = styled.div`
  font-weight:500;
`

export const RandomInformationContainer = styled.div`
  
`
