import styled from 'styled-components'


export const ButtonContainer = styled.div`
  position:relative;
`;

export const CustomButton = styled.div`
  border-radius:2px;
  box-shadow:hsla(0, 0%, 0%, 0.14) 0px 2px 2px 0px;
  color:hsl(0, 0%, 100%);
  display:inline-block;
  font-size:14px;

  letter-spacing:0.5px;
  line-height:36px;
  padding:0px 10px;
  margin:0 1rem;
  text-align:center;
  text-transform:uppercase;

  opacity:0.7;
  transition:300ms;

  :hover{
    opacity:1;
    transform:scale(1.2);
  }
`;