import React from 'react'
import { Container,Title,Wrapper } from './FeedbackSummary_styled'
import { ReactComponent as Positive } from 'Images/positive-thinking.svg';
import { ReactComponent as Negative } from 'Images/negative-thinking.svg';
import Feedback from './Feedback/Feedback';

function FeedbackSummary() {
  return (
    <Container>
      <Title>Recomendaciones <span>02</span></Title>
      <Wrapper>
        <Feedback><Positive /> </Feedback>
        <Feedback><Negative />  </Feedback>
        <Feedback><Negative />  </Feedback>
      </Wrapper>
    </Container>
  )
}

export default FeedbackSummary
