
import styled from 'styled-components'

export const Container = styled.div`
  
  ::after{
    content:'';
    display: block;
    border-bottom:solid 1px rgba(164,158,147,.22);
    padding-bottom: 2px;
  }

  cursor:pointer;
  transition:300ms;

  opacity:0.9;
  
  :hover{
    transform:scale(1.1);;
  }

`;



export const InfoContainer = styled.div`
  position: relative;
  padding:1rem 2rem;
`;

export const Name = styled.span`
  display: block;
  font-size:1.5rem;
  color:#252a34;
`;

export const Email = styled.span`
  display: block;
  color:#24a5a3;
`;

export const NextIcon = styled.a`
  position:absolute;
  top: .8rem;
  right: 2rem;
  border-radius:50%;
  color:#d6d4d4;
  font-size:3em;

`;

export const ChildrenContainer = styled.div`
  min-height: 100vh;
  
  width: 25rem;
  position:fixed;
  top:3.5rem;
  background-color:white;
  z-index:1;

 

  animation: slide-in-right 300ms;

  @keyframes slide-in-right {
  0% {
    transform: translateX(40px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
`;

export const BackButton = styled.span`
  font-family: "Montserrat","Helvetica",Arial,sans-serif;
  font-size:2rem;
  padding:1rem 1.5rem;
  color:white;
`

export const TopBar = styled.div`
  padding:.5rem;
  background-color:black;
  cursor:pointer;
  transition:300ms;

  :hover{
    background-color:#2d2d2d;
  }
`;

export const Span = styled.span`
  font-size:1.8rem;
  color:#f5b022;
`;


export const RouteCount = styled.span`
  position:absolute;
  top: 2.5rem;
  left: 20rem;
  border-radius:50%;

  padding:0 .5rem;
  color:white;
  background-color:#393e46;
  font-size:1.2em;
`;
