import React,{useState,useEffect} from 'react'
  
import { Container,InputSectionContainer,InputSection,InputContainer,Input,InputLabel,BackgroundImage,CustomButton } from './UserEditSection_styled'
import Test from 'Images/download.jpg'
import { connect } from 'react-redux'
import { updateUser } from '../../API'
import { saveUser } from 'Redux/UserReducer'


function UserEditSection({userInfo,loadUser}) {
  
  const { isAdmin,user:userLocalInfo } = userInfo;

  const [user,setUser] = useState({
      name:'',
      lastName:'',
      oldPassword:'',
      newPassword:'',
  })

  useEffect(()=>{
    setUser({
      ...user,
      name:userLocalInfo.name,
      lastName:userLocalInfo.lastName,
      email:userLocalInfo.email
    })

  },[userLocalInfo])

  const handleClick = ()=>{
    updateUser(user)
    .then((res)=>{
      console.log(res.data)
      localStorage.setItem('user',JSON.stringify(res.data))
      loadUser()
    })
    .catch((err)=>{console.log(err)})
    
  }
  
  const handleChange = (evt)=>{
    evt.preventDefault();
    const name = evt.target.name;
    const value = evt.target.value;

    setUser({
      ...user,
      [name]:value
    });
  }

  return (
    <Container>
      <BackgroundImage> <img src={Test} alt="No imag"/></BackgroundImage>

      <InputSectionContainer>
        <div style={{width:'60%'}}>
          <InputSection>
            <InputContainer>
              <InputLabel>Nombre</InputLabel>
              <Input placeholder='Nombre' name="name" value={user.name} onChange={handleChange}/>
            </InputContainer>

            <InputContainer>
              <InputLabel>Antigua Contraseña</InputLabel>
              <Input type="password" placeholder='Antigua Contraseña' name="oldPassword" value={user.oldPassword} onChange={handleChange}/>
            </InputContainer>
          </InputSection>  
        </div>
      </InputSectionContainer>

      <InputSectionContainer>
        <div style={{width:'60%'}}>
          <InputSection>
            <InputContainer>
              <InputLabel>Apellido</InputLabel>
              <Input placeholder='Apellido' name="lastName" value={user.lastName} onChange={handleChange}/>
            </InputContainer>

            <InputContainer>
              <InputLabel>Nueva Contraseña</InputLabel>
              <Input type="password" placeholder='Nueva Contraseña' name="newPassword" value={user.newPassword} onChange={handleChange}/>
            </InputContainer>
          </InputSection>  
        </div>
      </InputSectionContainer>

      <InputSectionContainer>
        <div style={{width:'60%'}}>
          <InputSection>
            <InputContainer>
              <InputLabel>Correo Electronico</InputLabel>
              <Input type="email" placeholder='Correo Electronico' name="email" value={user.email} onChange={handleChange}/>
            </InputContainer>

            <InputContainer>
              <CustomButton onClick={handleClick}>Guardar</CustomButton>
            </InputContainer>
          </InputSection>  
        </div>
      </InputSectionContainer>
      
    </Container>
  )
}

const mapRootStateToProps = state=>{
  return {
    userInfo:state.UserReducer
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
    loadUser:(user)=>{dispatch(saveUser(user))}
  }
}

export default connect(mapRootStateToProps,mapDispatchToProps)( UserEditSection )
