import styled from 'styled-components';
import { motion } from 'framer-motion';

export const Custom_Message = styled.div`
    animation:pulsate-fwd repeat;
    z-index:1;
    position:relative;
    top:50%;
    font-size:2rem;
    justify-content:center;
    text-align:center;
    align-items:center;

`;
export const LeftContainerWrapper = styled(motion.div)`
    min-width:400px;
    width:20%;
    @media (max-width: 868px) {
        display:none;
    }
`;


export const Container = styled.div`
    position: fixed;
    left:0;
    top: 0;
    width: 20%;
    height:100%;
    box-shadow:hsla(0, 0%, 0%, 0.2) 0px 2px 22px 0px;

    z-index:1;

    min-width:400px;

    background-color:white;


    
    --scrollbarBG: #CFD8DC;
    --thumbBG: #90A4AE;

    ::-webkit-scrollbar {
        width: 10px;
        display: none;
    }
    
        scrollbar-width: thin;
        scrollbar-color: var(--thumbBG) var(--scrollbarBG);
    
    ::-webkit-scrollbar-track {
        background: var(--scrollbarBG);
    }
    ::-webkit-scrollbar-thumb {
        background-color: var(--thumbBG) ;
        border-radius: 6px;
        border: 3px solid var(--scrollbarBG);
    }


`;

export const CustomH1 = styled.h1`
    font-size: 5rem;
    color:black;
    margin-left:1rem;
`;



export const Input1 = styled.input`
    border: 1px solid black;
    border-radius: 20px;
    color: #222;
    font-size: 20px;
    padding: 0.4rem 0.4rem;
    transition: 0.25s;

    :focus {
        outline: 2px dashed #945;
        outline-offset: 3px;
    }
`;
