import React from 'react'
import { connect } from 'react-redux'
import { deAnimateMarker, animateMarker, markerReferenceMouseOut, markerReferenceMouseIn, toggleStopsContainer } from 'Redux/ReduxActions'
import { deleteMarkerPolylineRoutePosition } from 'Redux/CombinedActions'
import { ReactComponent as Clear} from 'Images/clear.svg'

import { AnimatePresence } from 'framer-motion';

import { Container,StopDeleteIconContainer,StopsContainer,StopInfo,StopWrapper,NoStopsContainer,NoStopsMessage}  from './Stops_styled'

function Stops(props) {
  const { generalState:{MapControl:{stopsVisible}} } = props;
  const { route:{position} } =  props.route
  const { onAnimateMarker,onDeAnimateMarker,markerReferenceIn,markerReferenceOut,deleteMPRouteP } = props;
  const onMouseEnterHandler = (coords,evt)=>{
    const lat = coords[0];
    const lng = coords[1];
    onAnimateMarker({lat,lng});
    markerReferenceIn(coords)
  }

  const onMouseLeaveHandler = (coords,evt)=>{
    
    const _lat = coords[0];
    const _lng = coords[1];
    markerReferenceOut()
    onDeAnimateMarker({_lat,_lng}) 
  }

  const containerVariant = {
    hidden:{scaleY:0,transformOrigin:'100% 0%'},
    show:{scaleY:1,transformOrigin:'100% 0%',transition:{type:'spring',duration:1}},
    exit:{scaleY:0,transformOrigin:'100% 1%'}
  }

  const stopVariant = {
    hidden:{x:-30,opacity:0},
    show:{x:0,opacity:1},
    exit:{x:400}
  }

  return (
    <AnimatePresence>
      {stopsVisible && 
      <Container 
        variants={containerVariant} 
        initial='hidden'
        animate='show'
        exit='exit'>
      
        {position.length > 0 
        ? //if
        <StopWrapper >
          <AnimatePresence >
            {position.map((stop,index)=>{
              return (
                  <StopsContainer 
                    key={`stop-${index}`}
                    variants={stopVariant}
                    initial='hidden'
                    animate='show'
                    exit='exit'

                    onMouseEnter={onMouseEnterHandler.bind('',stop.LatLng)} 
                    onMouseLeave={onMouseLeaveHandler.bind('',stop.LatLng)}>

                    <StopInfo >{stop.streetName.substring(0,15)}...</StopInfo>
                    <StopDeleteIconContainer onClick={deleteMPRouteP.bind('',stop.LatLng)}> <Clear /> </StopDeleteIconContainer>
                  </StopsContainer>
                
              )
            })}
          </AnimatePresence>
        </StopWrapper> 
        : <NoStopsContainer>
            <div style={{padding:'1rem',textAlign:'center'}}>
              <NoStopsMessage>No hay rutas registradas.</NoStopsMessage>
              <NoStopsMessage>Click en el mapa para agregar rutas</NoStopsMessage>
            </div>
              
          </NoStopsContainer>}

      </Container>    
    }
    </AnimatePresence>
  )
}

const MapRootStateToProps = (states)=>{
  return {
      route:states.RouteReducer,
      generalState:states.GeneralReducer,
  }
}

const MapDispatchToProps = (dispatch)=>{
  return{
      toggleStops:()=>{dispatch(toggleStopsContainer())},
      deleteMPRouteP: (event)=>{dispatch(deleteMarkerPolylineRoutePosition(event))},
      markerReferenceIn: (coords)=>{dispatch(markerReferenceMouseIn(coords))},
      markerReferenceOut: ()=>{dispatch(markerReferenceMouseOut())},
      onAnimateMarker:(coords)=>{dispatch(animateMarker(coords))},
      onDeAnimateMarker: (coords)=>{dispatch(deAnimateMarker(coords))}
  }
}
export default connect (MapRootStateToProps,MapDispatchToProps)(Stops)
