import React from 'react'
import styled from 'styled-components';
import CustomButton from '../CustomButton/CustomButton'


const Container = styled.div`
  text-align:center;
  
`;

const ButtonContainer= styled.div`
  
  text-align:center;
`;

const Header2 = styled.h2`
  font-size:2rem;
  margin-bottom:1rem;
`;

function DeleteConfirmation({title="Usuario",confirm,cancel,message}) {
  return (
    <Container>
      <Header2 >{title} </Header2>
      <h2 style={{fontSize:'1.2rem'}} >{message}</h2>
      <ButtonContainer>
        <CustomButton text="Confirmar" _function = {confirm}/>
        <CustomButton text="Cancelar" _function = {cancel}/>
      </ButtonContainer>
    </Container>
  )
}

export default DeleteConfirmation
