import React from 'react';

import {ReactComponent as Mail} from 'Images/mail.svg'
import {ReactComponent as Lock} from 'Images/lock.svg'
import {ReactComponent as Account} from 'Images/account.svg'

export const createInputArray = (formik,register)=>{
  if (register === true) { 
    return[
      {
        placeholder:"Nombre",
        name:"name",
        icon:<Account />,
        value:formik.values.name,
        customFunction:formik.handleChange
      },
      {
        placeholder:"Apellido",
        name:"lastName",
        icon:<Account />,
        value:formik.values.lastName,
        customFunction:formik.handleChange
      },
      {
        placeholder:"Correo",
        name:"email",
        icon:<Mail />,
        value:formik.values.email,
        customFunction:formik.handleChange
      },
      {
        placeholder:"Contraseña",
        name:"password",
        icon:<Lock />,
        value:formik.values.password,
        customFunction:formik.handleChange,
        type:"password"
      }
    ]}else{ return [
      {
        placeholder:"Correo",
        name:"email",
        icon:<Mail />,
        value:formik.values.email,
        customFunction:formik.handleChange
      },
      {
        placeholder:"Contraseña",
        name:"password",
        icon:<Lock />,
        value:formik.values.password,
        customFunction:formik.handleChange,
        type:"password"
      }
    ]
  }
}


//Variants
export const containerVariants = {
  animateBackground:{
    backgroundPosition:['0%','70%','100%','70%','50%','0%'],
    transition:{duration:20,loop: Infinity}
  }
}