import React,{Fragment} from 'react'

import Login from './Pages/Login/Login';


import { connect } from 'react-redux'

import { Route,Redirect,Switch } from 'react-router-dom';

import Main from './Pages/AdminPage/Main/Main';
import { getLocalStorageInfo } from './Lib/global_functions';

function Authorized(){
  return(
    <Fragment>
        <Route path='/main' component={Main} />
        <Route exact path='/' component={Login}/> 
    </Fragment>
    
  )
}

function Unauthorized(){
  return(
    <Fragment>
      <Redirect to='/'/>
      <Route exact path='/' component={Login}/> 
    </Fragment>
  )
}

function Router({LogInfo}) {
return (
  <Fragment>
    <Switch>
      {getLocalStorageInfo().isAdminOrProvider()
        ?<Authorized />
        :<Unauthorized to="/" />
      }
    </Switch>   
  </Fragment>
)}


const mapRootStateToProps = state=>{
  return {
    LogInfo:state.LoginReducer
  }
}



export default connect(mapRootStateToProps)(Router)
