import React,{Fragment} from 'react'

import User from './Components/User';
import { connect } from 'react-redux';
import { Container } from './UserList_styled'
import LoadingAnimation from './LoadingAnimation';


function UserList({users:{users,loadingUsers}}) {
  return (
    <Container>
        {loadingUsers 
          ?<LoadingAnimation />
        :
          <Fragment>
              {users.map((userData,index)=>{ 
                return <User key={index+3} userData={userData} />
              })}
          </Fragment>
        }
    </Container>
  )
}

const mapRootStateToProps = state=>{
  return {
    users:state.UserReducer
  }
}

export default connect(mapRootStateToProps)(UserList)


