import * as types   from './GeneralTypes'

const initState = {
  form: {
      transportType:'bus'
  },
  //selected, hovered, normal
  routeState:'normal',
  hoveredPolylineCoordinates:[],
  hoverMarker:[],
  confirmationMessage:false,
  routeIdToDelete:'',
  generalState:{
      //post,put
      postUpdateButton:'post',
      routeHover:false,
      routeSelected:false,
      //form,route
      tabs:'form',
      //bus,carro,minibus
      activeButtonGroup:'bus'
  },
  AdminPage:{
    containerStyle:{
      display:'none'
    },
    backdropStyle:{
      display:'none'
    }
  },
  MapControl:{
    stopsVisible:false
  }
}



const GeneralReducer = (state = initState, action) =>{
  switch (action.type) {
    case types.CHANGE_STOPS_VISIBILITY:
      return {
        ...state,
        MapControl:{
          ...state.MapControl,
          stopsVisible:!state.MapControl.stopsVisible
        }
      }
    case types.UNLOAD_GENERAL:
      return initState
    case types.CHANGE_CONFIRMATION_MESSAGE:
      const {confirmationMessage:value,routeId}  = action.payload;
      
      return{
        ...state,
        routeIdToDelete:routeId,
        confirmationMessage:!value
      }
    case types.MARKER_REFERENCE_MOUSE_IN:
      const LatLng = action.coords;
      return {
        ...state,
        hoverMarker:LatLng
      }
    case types.MARKER_REFERENCE_MOUSE_OUT:
      return {
        ...state,
        hoverMarker:[]
      }
    case types.RESET_GENERAL_SETTINGS:
      return initState
    case types.CHANGE_POST_TO_PUT_BUTTON:
      return{
        ...state,
        generalState:{
          ...state.generalState,
          postUpdateButton:'put'
        }
      }
    case types.CHANGE_PUT_TO_POST_BUTTON:
      return{
        ...state,
        generalState:{
          ...state.generalState,
          postUpdateButton:'post'
        }
      }
    case types.MOUSE_IN:
      const { trayectory} = action.route;

      return{
        ...state,
        routeState:'hovered',
        hoveredPolylineCoordinates:trayectory.map((coords,index)=>{
          const [lat,lng] = coords.coordinates;
          
            return {
              lat:lat,lng:lng 
            };
        })
      }
    case types.MOUSE_OUT:
        return{
          ...state,
          routeState:'normal'
        }
    case types.FORM_TAB_CLICK:
      return {
        ...state,
        generalState:{
          ...state.generalState,
          tabs:'form'
        }
      }
    case types.ROUTE_TAB_CLICK:
      return{
        ...state,
        generalState:{
          ...state.generalState,
          tabs:'route'
        }
      }
    case types.CHANGE_TRANSPORT_TYPE:
      const transportType = action.transportType;
      return{
        ...state,
        generalState:{
          ...state.generalState,
          activeButtonGroup:transportType
        }
      }
    default:
      return state;
  }
  
}

export {GeneralReducer};
