import bddHelper from '../../BDD/Request'
import { unloadApp } from '../CombinedActions'
import { savingDataInState } from '../RoutesReducer';
import { routeLoading, routeSuccess, routeError } from '../RouteReducer';
import { unloadMap } from '../MapReducer';
const baseURL = `https://safe-inlet-31030.herokuapp.com/api/newroute/allRoutes`;
const url = `https://safe-inlet-31030.herokuapp.com/api/newroute`;



export const getRoutes = ()=>{

  return (dispatch)=>{
    bddHelper.getRequest(baseURL)
    .then((data)=>{
      dispatch ( savingDataInState(data) )
    })
    .catch((err)=>{
      console.log(err)
    })
  }
}

export const postRoute = (route)=>{

  return ((dispatch)=>{
    dispatch ( routeLoading() );

    bddHelper.postRequest(url,route)
      .then((data)=>{ 
        dispatch(unloadApp())
        dispatch ( routeSuccess() );
        dispatch(getRoutes())
      }).catch((err)=>{
        console.log(err)
        dispatch ( routeError() );
      })
  })
}


export const putRoute = (route,positionInfo,didMarkerChange)=>{
  let redoTrayectory = false;

  if(didMarkerChange){redoTrayectory = true}
  if (positionInfo.newPositionLength > positionInfo.positionLength){
    redoTrayectory = true
  } 
  if (positionInfo.newPositionLength < positionInfo.positionLength){
    redoTrayectory = true
  } 

  return ((dispatch)=>{
    dispatch( routeLoading() );

      bddHelper.putRequest(url,route,redoTrayectory)
      .then((res)=>{
        dispatch(unloadApp())
        dispatch(getRoutes())
        dispatch ( routeSuccess() );
      })
      .catch((err)=>{
        console.log(err)
        dispatch ( routeError() );
      })
  })
}

export const deleteRoute = (routeId)=>{
  return (dispatch)=>{
    dispatch ( routeLoading() );

    bddHelper.deleteRequest(url,routeId)
    .then((data)=>{
      dispatch( getRoutes() )
      dispatch ( routeSuccess() );
      dispatch( unloadMap() )
    })
    .catch((err)=>{
      console.log(err)
      dispatch ( routeError() );
    })
  }
}



