import * as types from './PolylineTypes'

export const addingPolyline = (coords)=>{
  return{
    type:types.ADD_POLYLINE,
    coords:coords
  }
}


export const updatingPolyline = (refData)=>{
  return {
    type: types.UPDATE_POLYLINE,
    refData:refData
  }
}

export const DeletePolyline = (event)=>{
  return {
    type:types.DELETE_POLYLINE,
    event:event
  }
}


export const fillingPolyline = (currentRoute)=>{
  return {
    type: types.FILL_POLYLINE,
    currentRoute:currentRoute
  }
}


export const unloadPolyline = ()=>{
  return {
    type: types.UNLOAD_POLYLINE
  }
}

export const togglePolylineVisibility = ()=>{
  return{
    type: types.TOGGLE_POLYLINE_VISIBILITY
  }
}
