import styled from 'styled-components';

export const DetailContainer = styled.div`  
    position:relative;
    display:flex;
    flex-direction:column;
    font-weight:700;

    animation:fadeIn 1s;

    color:black;
    padding:1rem;
    margin:2rem 1rem;
    border-radius:20px;
    overflow:hidden;
    
    box-shadow:hsla(0, 0%, 15%, 0.1) 0px 1px 15px 1px;

    transition:200ms;
    
    cursor: pointer;
    :hover{
        background-color:#fccd88;
    }
`;

export const BackgroundImage = styled.div`
    z-index: -1;
    svg{
        position:absolute;
        width: 300.89px;
        height: 400.94px;
        right: -105.64px;
        top: -5rem;
    
        fill: rgba(250, 125, 9, 0.06);
        filter: blur(4px);
        transform: rotate(37.55deg);  
    }
`;


export const RouteNameContainer = styled.div``;

export const RouteName = styled.span`
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size:1.5rem;
    font-weight:500;
    display: flex;
    align-items: center;
    
    color: #092532;

`;
export const RouteDetailWrapper = styled.div`
    display:flex;
    flex-direction:column;
`