import React from 'react'
import { connect } from 'react-redux'
import Select from 'react-select'

import { handleInputChange } from 'Redux/RouteReducer'
import {Container,DropDownContainer,CustomInput,CustomLabel,CustomSpan } from './EditableDropDown_styled'

const labelStyle = "block text-gray-700 text-lg font-bold mb-2";



function EditableDropDown(props) {
  const { /*/onRouteChange,style,containerStyle,editable=true,*/
    options=[],inputData,_function } = props;
  const { /*pattern,placeholder,size,value,type,*/label,name } = inputData;

  const createNewOptions = ()=>{
    return options.map((option)=>{
      return {value:option.value,label:`${option.value}`}
    })
  }

  const newOptions = createNewOptions()
  
  return (
    <Container >
      <CustomLabel className={labelStyle} > {label} </CustomLabel>
      <Select  
        defaultValue={newOptions[0]}
        name={name}
        onChange={_function}
        options={ newOptions }>
      </Select>
    </Container>
  )
}

const mapRootStateToProps = state=>{
  return {
      states:state
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
      onRouteChange: (event)=>{dispatch(handleInputChange(event))},
  }
}


export default connect (mapRootStateToProps,mapDispatchToProps)(EditableDropDown)

