import * as types from './PolylineTypes'

const initState = {
  options:{
    visible:true,
    strokeColor: '#EF6C00',
    strokeOpacity: 1,
    strokeWeight: 5,
  },
  polylineCoordinates : [ ]
}



const PolylineReducer = (state = initState, action) =>{
  switch (action.type) {
    case types.TOGGLE_POLYLINE_VISIBILITY:

      return {
        ...state,
        options:{
          ...state.options,
          visible:!state.options.visible
        }
      }
    case types.UPDATE_POLYLINE:
      const { lat:updated_lat,lng:updated_lng,
            currentPosition:{lat:current_lat,lng:current_lng}} = action.refData;
      
      return {
        ...state,
        polylineCoordinates:state.polylineCoordinates.map((polyline,index)=>{  
          if(current_lat === polyline.lat){
            if(current_lng === polyline.lng){
              return {
                lat:updated_lat,lng:updated_lng
              }
            }else{
              return polyline
            }   
          }else{
            return polyline
          }
        })
      }
    case types.UNLOAD_POLYLINE:
      return{
        ...state,
        polylineCoordinates:[]
      }
    case types.FILL_POLYLINE:   
      let {trayectory} = action.currentRoute

      return {
        ...state,
        polylineCoordinates:trayectory.map((data,index)=>{
          //const [lat,lng] = routeFromChild.LatLng; 
          const lat = data.coordinates[0]
          const lng = data.coordinates[1]
            return {
              lat:lat,lng:lng 
            };
        })
      };
    case types.ADD_POLYLINE:
      const { lat,lng } = action.coords;
      return{
        ...state,
        polylineCoordinates : [
          ...state.polylineCoordinates,
          { lat:lat,lng:lng }
        ]
      }
    case types.DELETE_POLYLINE:
      const coordinatesToDelete = action.event.target.elements[0].value;
      return{
        ...state,
        polylineCoordinates : [
          ...state.polylineCoordinates.filter( (coordinates,index)=>{
              // eslint-disable-next-line
              return coordinatesToDelete == coordinates.lat    
            }
          )
        ]
      }
      
    default:
      return state;
  }
  
}

export {PolylineReducer};