import React from 'react'

import HeaderSection from './Components/HeaderSection/HeaderSection';
import BottomSection from './Components/BottomSection/BottomSection';

import { Container,ChildrenContainer } from './CardContainer_styled'

const initState = {
  title:"Missing Info",topTitle:"Missing Info",bottomTitle:"Missing Info"
}

function CardContainer({children,cardInfo=initState,style}) {
  return ( 
      <Container style={style}>
        <HeaderSection tittle={cardInfo.title} topTitle={cardInfo.bottomTitle}/>
          <ChildrenContainer>
            {children}
          </ChildrenContainer>
        <BottomSection bottomTitle={cardInfo.bottomTitle}/>
      </Container>
    
  )
}

export default CardContainer
