import axios from "axios";
import { getUserTokenFromLocalStore } from "Lib/global_functions";

export const sendFeedback = (data)=>{

  const { name,lastName,email,phone='',message} = data;
  const curedData = {name:name,lastName:lastName,contactInfo:{email:email,telephone:phone},reason:message};

  const token = getUserTokenFromLocalStore();
  return axios(
    {
      method:'post',
      url:`https://safe-inlet-31030.herokuapp.com/api/contact`,
      headers:{
          'key':process.env.REACT_APP_DOBLE_VIA_KEY,
          'authorization':`Bearer ${token}`
      },
      data:curedData
    })
}