import * as types from './UserTypes';
import bddHelper from 'BDD/Request';
import { getLocalStorageInfo } from 'Lib/global_functions';

export const getUsersWithRoutes = ()=>{
  return(dispatch=>{
    dispatch(loadingUsers())
    bddHelper.UsersWIthRoutes()
    .then((res)=>{
      dispatch(saveUsers(res))
    })
    .catch((err)=>{
      console.log(err)
    })
  })
}

export const saveUsers = (users)=>{
  return{
    type:types.SAVE_USERS,
    payload:users
  }
}

export const loadingUsers = ()=>{
  return{
    type:types.LOADING_USERS
  }
}

export const saveUser = ()=>{
  const isAdmin = getLocalStorageInfo().isAdmin();
  const user = getLocalStorageInfo().user;
  
  return(dispatch=>{
    dispatch({type:types.SAVE_USER,payload:user})
    isAdmin && dispatch( setIsAdminToTrue() )
  })
}


export const unloadUser = ()=>{
  return {
    type:types.UNLOAD_USER
  }
}


export const setIsAdminToTrue = ()=>{
  return{
    type:types.IS_ADMIN_TRUE
  }
}

export const setIsAdminToFalse = ()=>{
  return{
    type:types.IS_ADMIN_FALSE
  }
}