import React from 'react'
import  { ReactComponent as Rate } from 'Images/starGroup.svg';
import {RouteReviewIcon,StarBackground,StarBackgroundContainer} from './Rating_styled'

function Rating({punctuation}) {
  return (
    <RouteReviewIcon >
      <StarBackgroundContainer >
        <StarBackground style={{width:`${punctuation}rem`}}/>
      </StarBackgroundContainer>
      <Rate />
    </RouteReviewIcon>
  )
}

export default Rating
