import styled from 'styled-components'
export const ButtonContainer = styled.div`
  position:relative;
`;

export const NameInitialsContainer = styled.div`
  cursor:pointer;
  background-color:#f5b022;
  text-align:center;
  display:flex;
  justify-content:center;
  align-items:center;
  padding:.4rem .4rem;
  border-radius:50%;
  transition:400ms;
  height: 2.5rem;
  width: 2.5rem;
  font-size:1rem;
  :hover{
    transform:scale(1.2)
  }


`;
export const NameInitial = styled.span`
  text-transform:uppercase;
  padding:.1rem;
`;
