
import styled from 'styled-components'
import { motion } from "framer-motion"


export const Container = styled(motion.div)`
  width: 100%;
  min-height: 100vh;
  display: flex;

  justify-content: center;
  align-items: center;
  padding: 15px;
  
  background: linear-gradient(-45deg, #ee7752, #23a6d5, #23d5ab);
  background-size: 550% 550%;

  .child{
    animation: vibrate-1 0.3s linear  both;
  }

`;

export const ChildContainer = styled(motion.div)`
  position:relative;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction:column;

  justify-content: center;
  padding:33px;

  
  @keyframes vibrate-1 {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-2px, 2px);
  }
  40% {
    transform: translate(-2px, -2px);
  }
  60% {
    transform: translate(2px, 2px);
  }
  80% {
    transform: translate(2px, -2px);
  }
  100% {
    transform: translate(0);
  }
}


`;

export const ImageContainer = styled.div`
  background-color:#b2cbd9;
  height: 100%;

  width: 50%;

  border-radius: 20px 0 0 20px;
`;

export const FormContainer = styled(motion.div)`
  color:black;
  width: 290px;
  border-radius: 0 20px 20px 0;
`;


export const Header = styled(motion.h2)`
  display: block;
  font-family: 'Poppins', sans-serif;
  font-size: 39px;
  color: #333;
  line-height: 1.2;
  text-align: center;

  font-weight:500;
  padding-bottom: 49px;
  font-weight: 400;
`;



export const LoginButton = styled(motion.button)`
  font-size: 15px;
  font-weight:700;
  line-height: 1.5;
  color: #fff;
  text-transform: uppercase;
  width: 90%;
  height: 50px;
  border-radius: 25px;
  background: #357593;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
  margin: 1rem;
`;

export const CreateAccount = styled.button`
background-color:#e6e6e6;
text-align:center;
border-radius:25px;
display:inline-block;
margin:auto;

padding:1rem;
margin: 2rem;
`;

export const BottomContainer = styled.div`
 
 font-family: 'Poppins', sans-serif;
 display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 155px;
`;

export const Span = styled.span`
  font-family: Poppins-Regular;
  font-size: 14px;
  line-height: 1.5;
  color: #666;

  padding-bottom: 17px;
`

export const SignUp = styled(motion.a)`
  font-family: Poppins-Regular;
  font-size: 14px;
  line-height: 1.5;
  color: #333;
  text-transform: uppercase;

  touch-action: manipulation;

  text-decoration: none;
  background-color: transparent;


`

export const InputContainer = styled.div`
  background-color:white;
`;

export const ErrorMessage = styled(motion.div)`
  margin:auto;
  text-align:center;
  position:absolute;
  bottom:12rem;
  right:1rem;
  width: 90%;
  font-family: 'Poppins', sans-serif;
  color:red;
  font-size:1.1rem;
`;