import * as types from './LoginTypes';
import bddHelper from '../../BDD/Request';

const baseURL = `https://safe-inlet-31030.herokuapp.com/api/user`;

  
const matchingRoutes = (routesReceived=[],user)=>{
  const { ID:userId} = user;
  return {
    user:user,
    routes:routesReceived.filter((route)=>{
      return route.ownerID === userId 
    })
  }
}

export const UsersWIthRoutes = ()=>{
    return bddHelper.getAllUsers()
    .then((userAPI)=>{
        return bddHelper.getRequest('https://safe-inlet-31030.herokuapp.com/api/newroute/allRoutes')
        .then((routes)=>{
            return userAPI.map((data)=>{
              return matchingRoutes(routes,data)
            }) 
        })
        .catch((err)=>{
          console.log(err)
        })
    })
  }

export const createUser = (userData)=>{
  const {name,lastName,email,password,role,token} = userData;

  return(dispatch=>{
    bddHelper.CreateUser(name,lastName,email,password,role,token)
    .then((res)=>{
      dispatch(getAllUsers())
    })
    .catch((err)=>{
      console.log(err);
    })
  })
}
export const updateUser = (userData)=>{
  return (dispatch=>{
    dispatch(loadingUsers())
    bddHelper.UpdateUser(userData)
    .then((res)=>{
      dispatch(getAllUsers())
    })
    .catch((err)=>{
      console.log(err)
    })
  })
}
export const loggingUser = (payload)=>{
  const {email,password} = payload;
  return(dispatch=>{
    dispatch(loadingLogIn())
    bddHelper.Login(email,password)
    .then(res=>{
      if(res.data.ok===true){
        localStorage.setItem('user',JSON.stringify(res.data))

        //If is Admin
        res.data.resolve.role === 'ADMIN' && (getAllUsers(res.data.token))
        
        dispatch(LoadUser(res.data));
      }
    }).catch(err=>{
      console.log(err)
      dispatch(loadingLogInErrors(err))
    })
  })
}

export const LogoutUser = ()=>{
  return(dispatch=>{
    dispatch(clearLocalStore())
  })
}

export const registerUser = (payload)=>{
  const {name,lastName,email,password} = payload;
  return(dispatch=>{
    dispatch(loadingLogIn())
    bddHelper.Register(name,lastName,email,password,baseURL)
    .then(res=>{
        localStorage.setItem('user',JSON.stringify(res.data))
        dispatch(LoadUser(res.data));
    }).catch(err=>{
      console.log(err)
      dispatch(loadingLogInErrors(err))
    })
  })
}

export const getAllUsers = ()=>{
  return(dispatch=>{
    dispatch(loadingUsers())
    bddHelper.getAllUsers()
    .then((res)=>{
      dispatch(LoadUsers(res))
    })
    .catch((err)=>{
      console.log(err)
      dispatch(loadingUsersErrors(err))
    })
  })
}

export const LoadUsers = (users)=>{
  return{
    type:types.LOAD_USERS,
    payload:users
  }
}

export const LoadUser = (user)=>{
  console.log(user)
  return{
    type:types.LOG_USER,
    payload:user
  }
}

export const unloadUser = (payload)=>{
  return{
    type:types.UNLOAD_USER
  }
}

export const handleUserChange = (event,some)=>{
  if('label' in event){
    event.target = {};
    event.target.name = some.name
    event.target.value = event.value;
  }
  return{
    type:types.HANDLE_USER,
    payload:event
  }
}

export const saveToLocalStore = (userData)=>{
  return{
    type:types.SAVE_USER_LOCAL_STORE,
    payload:userData
  }
}

export const clearLocalStore = ()=>{
  return {
    type:types.DELETE_USER_LOCAL_STORE
  }
}

export const loadingLogIn = ()=>{
  return{
    type:types.LOADING_LOGIN
  }
}

export const loadingLogInErrors = (err)=>{
  return{
    type:types.LOADING_LOGIN_ERRORS,
    payload:err
  }
}

export const loadingUsers = ()=>{
  return{
    type:types.LOADING_USERS
  }
}

export const loadingUsersErrors = (err)=>{
  return{
    type:types.LOADING_USERS_ERRORS,
    payload:err
  }
}

export const unloadLogging = ()=>{
  return {
    type:types.UNLOAD_LOGGING
  }
}