import React from 'react'
import { ButtonContainer,CustomButton } from './NavbarButton_styled'
import {Link} from "react-router-dom";


function NavbarButton({buttonText,href,goTo="/main"}){
  return(
    <ButtonContainer>
      <CustomButton ><Link to={goTo}>{buttonText}</Link> </CustomButton>
    </ButtonContainer>
  )
}


export default NavbarButton
