import React,{useState, Fragment} from 'react'
import Routes from '../../Routes/Route';

import { Container,ChildrenContainer,RouteCount,InfoContainer,
  Name,Email,NextIcon,TopBar,BackButton,Span} from './User_styled';


const show = {
  display:'flex',flexDirection:'column'
}
const hide = {
  display:'none'
}

function User({userData}) {
  const { user:{name,email} } = userData;
  const [open,setOpen] = useState(false);

  const handleClick = ()=>{
    setOpen(!open)
  }

  return (
    <Fragment>
      <Container  onClick={handleClick}>
        <InfoContainer>
          <Name>{name}</Name>
          <Email>{email}</Email>
            <RouteCount>
              {userData.routes.length}
            </RouteCount>  
          <NextIcon href="#" >&#8250;</NextIcon>
        </InfoContainer>
        
      </Container>
      
      <ChildrenContainer style={open ? show : hide} >
        <TopBar onClick={handleClick} >
          <BackButton >{'<'} </BackButton><Span>{name}</Span> 
        </TopBar>
        <Routes routes={userData.routes} />
      </ChildrenContainer>
      
    </Fragment>
  )
}

export default User

