import React from 'react'
import {ReactComponent as Construction} from 'Images/construction.svg';
import {ReactComponent as Clear} from 'Images/clear.svg';

import { Container,ReviewContainer,HeaderContainer,
        Header1,StarContainer,Paragraph,NameContainer,Date,
        Name,Avatar,AvatarContainer,GroupIconContainer,IconContainer} from './Reviews_styled'
import avatar from 'Images/avatar.jpg'
import Rating from 'Components/Rating/Rating';



const svgClearStyle= {
  fill:'#f33816',
}
const svgConstructionStyle= {
 
  fill:'#aadaff'
}
function Reviews(props) {

  const { data } = props;
  const { timeStamp,userName,raiting,comment,reviewTitle} = data;

  const date = timeStamp.slice(5, 10);
  const time = timeStamp.slice(14,19);

  return (
    <Container>
      <AvatarContainer >
          <Avatar alt="" src={avatar}/>
      </AvatarContainer>
      <ReviewContainer>
      
        <HeaderContainer>
          <Header1>{userName}</Header1>
          <Name>{reviewTitle}</Name>
        </HeaderContainer>

        <Paragraph>{comment}</Paragraph>

        <StarContainer>
          <Rating punctuation={raiting}/>
        </StarContainer>
        

        
      </ReviewContainer>
      <NameContainer>
          <Date>{ date + " - " + time}</Date>
      </NameContainer>
      <GroupIconContainer>
          <IconContainer> <Clear style={svgClearStyle}/> </IconContainer>
          <IconContainer> <Construction style={svgConstructionStyle}/> </IconContainer>
        </GroupIconContainer>
    </Container>
  )
}

export default Reviews
