import React,{useEffect} from 'react'
import { connect } from 'react-redux'
import { useAlert } from "react-alert";
import { useState } from 'react';


import {
  FormInputContainer,
  CustomSubmitButton,
  CancelButton,
  CancelButtonContainer,
  Testing,
  LineDivider
} from './Form_styled';

import ButtonGroup from 'Components/ButtonGroup/ButtonGroup';

import { postRoute,putRoute } from 'Redux/AsyncActions/index'
import { handleInputChange,routeResetSavingStatus } from 'Redux/RouteReducer/index'
import { getRouteList } from './RouteList'
import { unloadApp } from 'Redux/CombinedActions'
import CustomInputContainer from './Components/CustomInputFormContainer/CustomInputFormContainer';
import CustomInputForm from './Components/CustomInput/CustomInputForm';
import EditableDropDown from '../EditableDropDown/EditableDropDown';



function Form (props){
  
  const alert = useAlert();
  const [intervals,setIntervals] = useState([
    {value:'5'},
    {value:'10'},
    {value:'15'},
    {value:'20'},
    {value:'25'},
    {value:'30'},
    {value:'35'},
  ])

  const { onRouteChange,addRoute,updateRoute,clear,resetSavingRouteStatus } = props;
  const { route,routeSavingStatus } = props.states.RouteReducer;
  
  const { generalState }  = props.states.GeneralReducer;
  const marker = props.states.MarkersReducer;
  
  const routeList = getRouteList(route);
  const {error,saved}=routeSavingStatus;

  const { positionLength,newPositionLength } = props.states.RouteReducer.routeInfo;

  const SubmitButton = (event)=>{
    event.preventDefault();

    if(generalState.postUpdateButton === 'post'){
      addRoute(route);
    }else{
      updateRoute(route,{positionLength,newPositionLength},marker.didMarkerChange);
    }
  }


  const handleCancelButton = event=>{
    event.stopPropagation()
    clear();
  }

  useEffect(()=>{
    error && alert.error('noooooooooooooo')
    //loading && alert.show('loading')
    saved && alert.success('Ruta Guardada')

    setTimeout(()=>{
      resetSavingRouteStatus()
    },3000)
  },[error,saved,alert,resetSavingRouteStatus])


  return (
    <FormInputContainer 
      className="formInputContainer"
      onSubmit={SubmitButton}
    >
      
      <CustomInputContainer>
        <CustomInputForm inputData={routeList.ID} style={{width:'50%'}}/>
        
        <CancelButtonContainer>
          <CancelButton type="button" onClick={handleCancelButton}>
            Limpiar
          </CancelButton>
        </CancelButtonContainer>
      </CustomInputContainer>


      <CustomInputContainer>
        <CustomInputForm inputData={routeList.routeName} />
      </CustomInputContainer>

      <Testing>
        <ButtonGroup />
      </Testing>

      <LineDivider />

      <CustomInputContainer>
        <CustomInputForm 
          inputData={routeList.price} 
          style={{width:'8rem',marginRight:'1rem'}}/>
  
        <EditableDropDown 
          _function={onRouteChange}
          form={true}
          options={intervals}
          inputData ={routeList.interval}/>
      </CustomInputContainer>
      
      <CustomInputContainer>
        <CustomInputForm inputData={routeList.destination} />
      </CustomInputContainer>

      <CustomInputContainer>
        <CustomInputForm inputData={routeList.startTime} style={{width:'9rem',marginBottom:'1rem'}}/>
        <CustomInputForm inputData={routeList.endTime} />
      </CustomInputContainer>

      <LineDivider />

      <CustomInputContainer>
        <CustomInputForm inputData={routeList.details} />
      </CustomInputContainer>

      <CustomInputContainer>
        <CustomInputForm  inputData={routeList.warnings} />
      </CustomInputContainer>
      
      <CustomSubmitButton 
        className="submit-button inline-block  text-white font-bold my-5 py-2 px-8 rounded ">
          { generalState.postUpdateButton === 'post'
          ? "Guardar"
          : generalState.postUpdateButton === 'put'
          && "Confirmar" }
      </CustomSubmitButton>  

    </FormInputContainer>
  )

}

const mapRootStateToProps = state=>{
  return {
      states:state,
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
      onRouteChange: (event,some,test)=>{dispatch(handleInputChange(event,some,test))},
      addRoute: (route)=>{dispatch(postRoute(route))},
      updateRoute: (route,position,didMarkerChange)=>{dispatch(putRoute(route,position,didMarkerChange))},
      clear: ()=>{dispatch(unloadApp())},
      resetSavingRouteStatus:()=>{dispatch(routeResetSavingStatus())}
  }
}


export default connect(mapRootStateToProps,mapDispatchToProps)(Form)

