
import styled from 'styled-components'
import motion from 'framer-motion'

export const Container = styled.div`
  position:relative;
  width:48rem;
`;

export const BackgroundImage = styled.div`
    position: relative;
    top: 0.1rem;
    height: 11rem;
    display:flex;
    justify-content:flex-end;
    align-items:flex-end;
    
  svg{
    width:100%;
    height:100%;
  }
`;

export const FloatButton = styled.span`
  background-color: #81A9BD;
  border-radius: 5px;

  color:white;
  padding:.4rem 3rem .4rem 1rem;

  cursor:pointer;

  position:absolute;
  bottom: 8rem;
`;

export const HeaderContainer = styled.div`
  margin-bottom:1rem;
`;


export const Header = styled.h3`
  font-weight: normal;
  font-size: 4.2rem;
  text-transform:uppercase;
  color:#393e46;
  /* identical to box height */

  letter-spacing: 0.08em;

  color: #000000;
  margin-left:.2rem;
`;

export const HeaderInfo = styled.p`
  font-family: 'Roboto-mono',sans-serif;
  font-weight: bold;
  font-size: 1rem;
  width: 20rem;
  letter-spacing: 0.13em;
  margin-left:.3rem;
  color: #888888;

`;


export const InputWrapper = styled.div`
  width:22rem;
`;
export const InputContainer = styled.p``;

export const Input = styled.input`
  width: 100%;
  border: 2px solid #000000;
  box-sizing: border-box;
  border-radius: 10px;

  padding:.3rem .3rem;
`;
export const InputLabel = styled.span`
  position:relative;
  left:.3rem;
  display: block;
  font-weight: bold;
  font-size: 1.2rem;
  letter-spacing: 0.13em;

  color: #196386;

`;



export const TextAreaContainer = styled.div`
  margin-bottom:.6rem;
`;
export const TextArea = styled.textarea`
  border: 2px solid #000000;
  box-sizing: border-box;
  border-radius: 10px;

  padding:.5rem;

  width: 100%;
  min-height:5rem;
`; 
export const TextAreaWrapper = styled.div``;
export const TextAreaLabel = styled.div`
  position:relative;
  left:.3rem;
  display: block;
  font-weight: bold;
  font-size: 1.2rem;
  letter-spacing: 0.13em;

  color: #196386;
`;

export const Image = styled.p`
  svg{
    position:absolute;
    right: -1rem;
    top: 3.8rem;
    height: 25rem;
    width: 25rem;
  }
`;


