import axios from "axios"


export const updateUser = (user)=> { 
  const local = JSON.parse(localStorage.getItem('user'));
  const token = local.token;
  const id = local.resolve.ID;

  console.log(user)
  return axios(
    {
      method:'put',
      url:`https://safe-inlet-31030.herokuapp.com/api/user/update/${id}`,
      headers:{
          'key':process.env.REACT_APP_DOBLE_VIA_KEY,
          'authorization':`Bearer ${token}`
      },
      data:user
    })

}