import * as types from './LoginTypes'

const initialState = {
  user:{
    loadingLogIn:false,
    loadingLogInError:{},
    logInError:false,

    loadingUsers:false,
    loadingUsersError:{},
    usersError:false,

    loggedIn:false,
    loggedOut:true,
    token:'',
    response:'',
    data:{},
    users:[],
    userInfo:{name:'',lastName:'',email:'',role:'',newPassword:'',oldPassword:''}
  }
}

const LoginReducer = (state = initialState, action)=>{
  
  switch (action.type) {
    case types.UNLOAD_LOGGING:
      return initialState
    case types.LOADING_LOGIN:
      return {
        user:{
          ...state.user,
          loadingLogIn:true,
          logInError:false
        }
      }
    case types.LOADING_LOGIN_ERRORS:
      const logError  = action.payload
      return {
        user:{
          ...state.user,
          loadingLogInError:logError,
          loadingLogIn:false,
          logInError:true
        }
      }
    case types.LOG_USER:
      const { token,resolve } = action.payload;
    
      return{
        user:{
          ...state.user,
          loggedIn:true,
          loggedOut:false,
          loadingLogIn:false,
          token:token,
          data:resolve
        }
      }
    case types.LOG_OUT:
      return {
        user:initialState
      }
    case types.HANDLE_USER:
      const  event  = action.payload;
      
      const name = event.target.name;
      const value = event.target.value;


      console.log(name,value,state.user.userInfo)

      return {
        user:{
          ...state.user,
          userInfo:{
            ...state.user.userInfo,
            [name]:value
          }
        }
      }

      case types.SAVE_USER_LOCAL_STORE:
        const { userData } = action.payload
        localStorage.setItem('user',userData)
      return state

      case types.DELETE_USER_LOCAL_STORE:
        localStorage.clear()
      return state
      case types.LOADING_USERS_ERRORS:
        const err  = action.payload

        return {
          user:{
            ...state.user,
            loadingUsersError:err,
            loadingUsers:false,
            usersError:true
          }
        }
      case types.LOADING_USERS:
        return{
          user:{
            ...state.user,
            loadingUsers:true
          }
        }
      case types.LOAD_USERS:
        const  users  = action.payload;
        return {
          user:{
            ...state.user,
            loadingUsers:false,
            users:users
          }
        }
      
    default: return state
  }
}

export {LoginReducer}