import React from 'react';
import { Switch, Route } from 'react-router-dom';
import CentralCards from 'Components/CentralCards/CentralCards';
import MainChildrenRoute from './MainChildrenRoute';
import { connect } from 'react-redux';

function MainRoute({path}) {
  return (
    <Switch>
      <Route exact path={`${path}`} >
        <CentralCards />  
      </Route>

      <Route exact path={`${path}/:idOf`} >
        <MainChildrenRoute />
      </Route>
    </Switch>
  )
}




export default connect() (MainRoute)
