import styled from 'styled-components'
import { motion } from 'framer-motion';

export const LoadingLoginContainer = styled.div`
  margin:auto;
`;
export const BounceBallWrapper = styled.div``;

export const BounceBallContainer = styled.div`
    position: relative;
    display: inline-block;
    height: 1rem;
    width: 10px;
    margin:1rem;
`;
export const BounceBallText = styled.div`
  font-family:Montserrat;
  text-transform:uppercase;
  color: #fbae17;
  position:relative;
  left:1.2rem;
`;

export const BounceBall = styled(motion.div)`
    position: absolute;
    top: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fbae17;
    transform-origin: 50%;
    animation: bounce 500ms alternate infinite ease;

    @keyframes bounce {
    0% {
      top: 30px;
      height: 5px;
      border-radius: 60px 60px 20px 20px;
      transform: scaleX(2);
    }
    35% {
      height: 15px;
      border-radius: 50%;
      transform: scaleX(1);
    }
    100% {
      top: 0;
    }
  }
`;
